import React, { createContext, useContext, useState, useEffect } from 'react';
import { useAuth } from './AuthContext';
import { useNotification } from './NotificationContext';

const ChatContext = createContext();

export const useChat = () => {
  const context = useContext(ChatContext);
  if (!context) {
    throw new Error('useChat must be used within a ChatProvider');
  }
  return context;
};

export const ChatProvider = ({ children }) => {
  const { user, isAuthenticated } = useAuth();
  const { showNotification } = useNotification();
  
  const [chatSessions, setChatSessions] = useState({});
  const [activeChatId, setActiveChatId] = useState(null);
  const [isOnline, setIsOnline] = useState(true);
  const [supportAgents, setSupportAgents] = useState([]);
  const [queuePosition, setQueuePosition] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  // Sample support agents
  const sampleAgents = [
    {
      id: 1,
      name: "Sarah Wilson",
      avatar: "https://api.dicebear.com/7.x/avatars/svg?seed=Sarah",
      status: "online",
      specialties: ["Order Issues", "Product Questions", "Technical Support"],
      rating: 4.9,
      responseTime: "< 2 min"
    },
    {
      id: 2,
      name: "Mike Chen",
      avatar: "https://api.dicebear.com/7.x/avatars/svg?seed=Mike",
      status: "online",
      specialties: ["Returns & Refunds", "Payment Issues", "Account Help"],
      rating: 4.8,
      responseTime: "< 3 min"
    },
    {
      id: 3,
      name: "Emily Rodriguez",
      avatar: "https://api.dicebear.com/7.x/avatars/svg?seed=Emily",
      status: "busy",
      specialties: ["Prime Benefits", "Shipping", "Product Recommendations"],
      rating: 4.9,
      responseTime: "< 5 min"
    }
  ];

  // Sample chat sessions for demonstration
  const sampleChats = {
    1: {
      id: 1,
      userId: 1,
      agentId: 1,
      agent: sampleAgents[0],
      status: "active",
      startTime: new Date(Date.now() - 15 * 60 * 1000), // 15 minutes ago
      topic: "Order Status Inquiry",
      priority: "medium",
      satisfaction: null,
      messages: [
        {
          id: 1,
          senderId: 1,
          senderType: "agent",
          content: "Hello! I'm Sarah from Amazon Customer Support. I see you have a question about your recent order. How can I help you today?",
          timestamp: new Date(Date.now() - 15 * 60 * 1000),
          type: "text"
        },
        {
          id: 2,
          senderId: user?.id || 1,
          senderType: "customer",
          content: "Hi Sarah! I placed an order yesterday (Order #AMZ-789123) and haven't received any shipping updates. Can you help me track it?",
          timestamp: new Date(Date.now() - 14 * 60 * 1000),
          type: "text"
        },
        {
          id: 3,
          senderId: 1,
          senderType: "agent",
          content: "I'd be happy to help you track your order! Let me look that up for you right now.",
          timestamp: new Date(Date.now() - 13 * 60 * 1000),
          type: "text"
        },
        {
          id: 4,
          senderId: 1,
          senderType: "agent",
          content: "Great news! Your order has been shipped and is currently in transit. It should arrive by tomorrow evening. I've sent you a tracking link via email.",
          timestamp: new Date(Date.now() - 5 * 60 * 1000),
          type: "text"
        }
      ]
    }
  };

  useEffect(() => {
    // Load chat sessions from localStorage
    const savedChats = localStorage.getItem('chatSessions');
    if (savedChats) {
      setChatSessions(JSON.parse(savedChats));
    } else {
      setChatSessions(sampleChats);
      localStorage.setItem('chatSessions', JSON.stringify(sampleChats));
    }

    setSupportAgents(sampleAgents);

    // Simulate real-time updates
    const interval = setInterval(() => {
      if (Math.random() < 0.1) { // 10% chance every 3 seconds
        simulateAgentMessage();
      }
    }, 3000);

    return () => clearInterval(interval);
  }, []);

  const startNewChat = async (topic = "General Inquiry", priority = "medium") => {
    if (!isAuthenticated) {
      showNotification('Please login to start a chat', 'error');
      return null;
    }

    setIsLoading(true);

    try {
      // Simulate finding available agent
      await new Promise(resolve => setTimeout(resolve, 2000));

      const availableAgent = supportAgents.find(agent => agent.status === "online");
      
      if (!availableAgent) {
        setQueuePosition(Math.floor(Math.random() * 5) + 1);
        showNotification('All agents are busy. You\'ve been added to the queue.', 'info');
        return null;
      }

      const newChatId = Date.now();
      const newChat = {
        id: newChatId,
        userId: user.id,
        agentId: availableAgent.id,
        agent: availableAgent,
        status: "active",
        startTime: new Date(),
        topic,
        priority,
        satisfaction: null,
        messages: [
          {
            id: 1,
            senderId: availableAgent.id,
            senderType: "agent",
            content: `Hello! I'm ${availableAgent.name} from Amazon Customer Support. I'm here to help you with your ${topic.toLowerCase()}. How can I assist you today?`,
            timestamp: new Date(),
            type: "text"
          }
        ]
      };

      const updatedChats = {
        ...chatSessions,
        [newChatId]: newChat
      };

      setChatSessions(updatedChats);
      setActiveChatId(newChatId);
      localStorage.setItem('chatSessions', JSON.stringify(updatedChats));
      
      showNotification(`Connected to ${availableAgent.name}`, 'success');
      return newChat;
    } catch (error) {
      showNotification('Failed to start chat session', 'error');
      return null;
    } finally {
      setIsLoading(false);
    }
  };

  const sendMessage = async (chatId, content, type = "text") => {
    if (!chatSessions[chatId]) return false;

    const newMessage = {
      id: Date.now(),
      senderId: user.id,
      senderType: "customer",
      content,
      timestamp: new Date(),
      type
    };

    const updatedChat = {
      ...chatSessions[chatId],
      messages: [...chatSessions[chatId].messages, newMessage]
    };

    const updatedChats = {
      ...chatSessions,
      [chatId]: updatedChat
    };

    setChatSessions(updatedChats);
    localStorage.setItem('chatSessions', JSON.stringify(updatedChats));

    // Simulate agent response
    setTimeout(() => {
      simulateAgentResponse(chatId, content);
    }, 2000 + Math.random() * 3000);

    return true;
  };

  const simulateAgentResponse = (chatId, customerMessage) => {
    if (!chatSessions[chatId]) return;

    const responses = [
      "I understand your concern. Let me look into this for you.",
      "That's a great question! Let me check our system for the most up-to-date information.",
      "I'm happy to help you with that. Give me just a moment to review your account.",
      "Thank you for providing that information. This helps me assist you better.",
      "I see what you mean. Let me walk you through the solution step by step.",
      "That's completely understandable. Here's what I can do to help you...",
      "I've found the information you need. Here are your options...",
      "Perfect! I've updated that for you. You should see the changes within 24 hours."
    ];

    const response = responses[Math.floor(Math.random() * responses.length)];
    
    const agentMessage = {
      id: Date.now(),
      senderId: chatSessions[chatId].agentId,
      senderType: "agent",
      content: response,
      timestamp: new Date(),
      type: "text"
    };

    const updatedChat = {
      ...chatSessions[chatId],
      messages: [...chatSessions[chatId].messages, agentMessage]
    };

    const updatedChats = {
      ...chatSessions,
      [chatId]: updatedChat
    };

    setChatSessions(updatedChats);
    localStorage.setItem('chatSessions', JSON.stringify(updatedChats));
  };

  const simulateAgentMessage = () => {
    if (!activeChatId || !chatSessions[activeChatId]) return;

    const tips = [
      " Tip: Did you know you can track all your orders in the 'Your Orders' section?",
      " Quick tip: Use our mobile app for faster customer service!",
      " Pro tip: Prime members get priority customer support!",
      " Reminder: You can start a return right from your order history.",
      " Helpful hint: Try our automated help center for instant answers!"
    ];

    if (Math.random() < 0.3) { // 30% chance to send a tip
      const tip = tips[Math.floor(Math.random() * tips.length)];
      
      const tipMessage = {
        id: Date.now(),
        senderId: chatSessions[activeChatId].agentId,
        senderType: "agent",
        content: tip,
        timestamp: new Date(),
        type: "tip"
      };

      const updatedChat = {
        ...chatSessions[activeChatId],
        messages: [...chatSessions[activeChatId].messages, tipMessage]
      };

      const updatedChats = {
        ...chatSessions,
        [activeChatId]: updatedChat
      };

      setChatSessions(updatedChats);
      localStorage.setItem('chatSessions', JSON.stringify(updatedChats));
    }
  };

  const endChat = async (chatId, satisfaction = null) => {
    if (!chatSessions[chatId]) return false;

    const updatedChat = {
      ...chatSessions[chatId],
      status: "ended",
      endTime: new Date(),
      satisfaction
    };

    const updatedChats = {
      ...chatSessions,
      [chatId]: updatedChat
    };

    setChatSessions(updatedChats);
    localStorage.setItem('chatSessions', JSON.stringify(updatedChats));

    if (activeChatId === chatId) {
      setActiveChatId(null);
    }

    showNotification('Chat session ended', 'info');
    return true;
  };

  const getChatHistory = () => {
    return Object.values(chatSessions)
      .filter(chat => chat.userId === user?.id)
      .sort((a, b) => new Date(b.startTime) - new Date(a.startTime));
  };

  const getActiveChat = () => {
    return activeChatId ? chatSessions[activeChatId] : null;
  };

  const reopenChat = (chatId) => {
    if (chatSessions[chatId] && chatSessions[chatId].status === "ended") {
      setActiveChatId(chatId);
      
      const updatedChat = {
        ...chatSessions[chatId],
        status: "active"
      };

      const updatedChats = {
        ...chatSessions,
        [chatId]: updatedChat
      };

      setChatSessions(updatedChats);
      localStorage.setItem('chatSessions', JSON.stringify(updatedChats));
    }
  };

  const getUnreadCount = () => {
    if (!activeChatId || !chatSessions[activeChatId]) return 0;
    
    // In a real app, you'd track read status
    const lastAgentMessage = chatSessions[activeChatId].messages
      .filter(msg => msg.senderType === "agent")
      .pop();
    
    return lastAgentMessage && 
           new Date() - new Date(lastAgentMessage.timestamp) < 30000 ? 1 : 0;
  };

  const value = {
    chatSessions,
    activeChatId,
    isOnline,
    supportAgents,
    queuePosition,
    isLoading,
    startNewChat,
    sendMessage,
    endChat,
    getChatHistory,
    getActiveChat,
    reopenChat,
    getUnreadCount,
    setActiveChatId
  };

  return (
    <ChatContext.Provider value={value}>
      {children}
    </ChatContext.Provider>
  );
};

export default ChatContext;