import React, { createContext, useContext, useState, useEffect } from 'react';
import { useProducts } from './ProductContext';
import { useAuth } from './AuthContext';
import { useAnalytics } from './AnalyticsContext';

const AdvancedSearchContext = createContext();

export const useAdvancedSearch = () => {
  const context = useContext(AdvancedSearchContext);
  if (!context) {
    throw new Error('useAdvancedSearch must be used within an AdvancedSearchProvider');
  }
  return context;
};

export const AdvancedSearchProvider = ({ children }) => {
  const { products } = useProducts();
  const { user } = useAuth();
  const { trackEvent } = useAnalytics();

  const [searchHistory, setSearchHistory] = useState([]);
  const [savedSearches, setSavedSearches] = useState([]);
  const [searchSuggestions, setSearchSuggestions] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [popularSearches, setPopularSearches] = useState([]);
  const [aiInsights, setAiInsights] = useState({});

  // Advanced search filters
  const [filters, setFilters] = useState({
    category: '',
    brand: '',
    priceRange: { min: 0, max: 100000 },
    rating: 0,
    availability: 'all', // all, in-stock, out-of-stock
    shipping: 'all', // all, free-shipping, prime, same-day
    seller: 'all', // all, amazon, third-party
    condition: 'all', // all, new, used, refurbished
    sortBy: 'relevance', // relevance, price-low, price-high, rating, newest
    discountPercentage: 0,
    location: '',
    features: []
  });

  // Sample data
  const samplePopularSearches = [
    { query: "iPhone 15", count: 15420, trend: "up" },
    { query: "wireless headphones", count: 12300, trend: "up" },
    { query: "laptop gaming", count: 9800, trend: "stable" },
    { query: "smart watch", count: 8900, trend: "down" },
    { query: "kitchen appliances", count: 7600, trend: "up" },
    { query: "fitness tracker", count: 6400, trend: "stable" },
    { query: "home security camera", count: 5800, trend: "up" },
    { query: "bluetooth speaker", count: 5200, trend: "stable" }
  ];

  const sampleAiInsights = {
    "iPhone 15": {
      intent: "product_specific",
      confidence: 0.95,
      suggestions: [
        "Consider iPhone 15 Pro for better camera features",
        "Check compatibility with your current accessories",
        "Compare with Samsung Galaxy S24 for alternatives"
      ],
      trends: {
        priceDropExpected: true,
        peakDemandSeason: "Holiday Season",
        relatedSearches: ["iPhone 15 case", "iPhone 15 screen protector", "iPhone charger"]
      }
    },
    "wireless headphones": {
      intent: "category_browse",
      confidence: 0.85,
      suggestions: [
        "Sony WH-1000XM5 for best noise cancellation",
        "AirPods Pro for Apple ecosystem integration",
        "Budget option: Anker Soundcore Liberty Air 2"
      ],
      trends: {
        priceDropExpected: false,
        peakDemandSeason: "Back to School",
        relatedSearches: ["noise cancelling", "bluetooth earbuds", "gaming headset"]
      }
    }
  };

  useEffect(() => {
    // Load saved data from localStorage
    const savedHistory = localStorage.getItem('searchHistory');
    const savedSearchesList = localStorage.getItem('savedSearches');

    if (savedHistory) {
      setSearchHistory(JSON.parse(savedHistory));
    }

    if (savedSearchesList) {
      setSavedSearches(JSON.parse(savedSearchesList));
    }

    setPopularSearches(samplePopularSearches);
    setAiInsights(sampleAiInsights);
  }, []);

  const performAdvancedSearch = async (query, customFilters = {}) => {
    setIsLoading(true);
    
    try {
      // Track search event
      trackEvent('advanced_search', {
        query,
        filters: { ...filters, ...customFilters },
        userId: user?.id
      });

      // Add to search history
      const newSearchEntry = {
        id: Date.now(),
        query,
        filters: { ...filters, ...customFilters },
        timestamp: new Date(),
        resultsCount: 0
      };

      const updatedHistory = [newSearchEntry, ...searchHistory.slice(0, 19)]; // Keep last 20 searches
      setSearchHistory(updatedHistory);
      localStorage.setItem('searchHistory', JSON.stringify(updatedHistory));

      // Apply filters to products
      let filteredProducts = products.filter(product => {
        // Text search
        const matchesQuery = query === '' || 
          product.title.toLowerCase().includes(query.toLowerCase()) ||
          product.description.toLowerCase().includes(query.toLowerCase()) ||
          product.category.toLowerCase().includes(query.toLowerCase()) ||
          product.brand?.toLowerCase().includes(query.toLowerCase());

        if (!matchesQuery) return false;

        // Apply filters
        const mergedFilters = { ...filters, ...customFilters };

        // Category filter
        if (mergedFilters.category && product.category !== mergedFilters.category) {
          return false;
        }

        // Brand filter
        if (mergedFilters.brand && product.brand !== mergedFilters.brand) {
          return false;
        }

        // Price range filter
        if (product.price < mergedFilters.priceRange.min || product.price > mergedFilters.priceRange.max) {
          return false;
        }

        // Rating filter
        if (product.rating < mergedFilters.rating) {
          return false;
        }

        // Availability filter
        if (mergedFilters.availability === 'in-stock' && !product.inStock) {
          return false;
        }
        if (mergedFilters.availability === 'out-of-stock' && product.inStock) {
          return false;
        }

        // Shipping filter
        if (mergedFilters.shipping === 'free-shipping' && !product.freeShipping) {
          return false;
        }
        if (mergedFilters.shipping === 'prime' && !product.prime) {
          return false;
        }

        // Discount filter
        const discountPercent = product.originalPrice ? 
          ((product.originalPrice - product.price) / product.originalPrice) * 100 : 0;
        if (discountPercent < mergedFilters.discountPercentage) {
          return false;
        }

        return true;
      });

      // Apply sorting
      const sortBy = customFilters.sortBy || filters.sortBy;
      filteredProducts = sortProducts(filteredProducts, sortBy);

      // Update results count
      newSearchEntry.resultsCount = filteredProducts.length;

      // Generate AI suggestions
      const suggestions = await generateSearchSuggestions(query, filteredProducts);
      setSearchSuggestions(suggestions);

      return {
        products: filteredProducts,
        totalResults: filteredProducts.length,
        suggestions,
        aiInsights: aiInsights[query.toLowerCase()] || null,
        appliedFilters: { ...filters, ...customFilters }
      };

    } catch (error) {
      console.error('Search error:', error);
      return {
        products: [],
        totalResults: 0,
        suggestions: [],
        aiInsights: null,
        error: error.message
      };
    } finally {
      setIsLoading(false);
    }
  };

  const sortProducts = (productList, sortBy) => {
    const sorted = [...productList];
    
    switch (sortBy) {
      case 'price-low':
        return sorted.sort((a, b) => a.price - b.price);
      case 'price-high':
        return sorted.sort((a, b) => b.price - a.price);
      case 'rating':
        return sorted.sort((a, b) => b.rating - a.rating);
      case 'newest':
        return sorted.sort((a, b) => new Date(b.createdAt || 0) - new Date(a.createdAt || 0));
      case 'popularity':
        return sorted.sort((a, b) => (b.sales || 0) - (a.sales || 0));
      case 'discount':
        return sorted.sort((a, b) => {
          const discountA = a.originalPrice ? ((a.originalPrice - a.price) / a.originalPrice) * 100 : 0;
          const discountB = b.originalPrice ? ((b.originalPrice - b.price) / b.originalPrice) * 100 : 0;
          return discountB - discountA;
        });
      default:
        return sorted; // relevance - already filtered
    }
  };

  const generateSearchSuggestions = async (query, results) => {
    // Simulate AI-powered suggestions
    await new Promise(resolve => setTimeout(resolve, 500));

    const suggestions = [];

    // Query refinement suggestions
    if (results.length > 100) {
      suggestions.push({
        type: 'refine',
        text: `Try adding "${query} brand" or "${query} under 5000" to narrow results`,
        icon: ''
      });
    }

    // Alternative search suggestions
    if (results.length < 5) {
      const alternatives = generateAlternatives(query);
      alternatives.forEach(alt => {
        suggestions.push({
          type: 'alternative',
          text: `Try searching for "${alt}" instead`,
          icon: ''
        });
      });
    }

    // Category suggestions
    const categories = [...new Set(results.map(p => p.category))];
    if (categories.length > 1) {
      suggestions.push({
        type: 'category',
        text: `Browse ${categories[0]} or ${categories[1]} specifically`,
        icon: ''
      });
    }

    // Price optimization suggestions
    if (results.length > 10) {
      const avgPrice = results.reduce((sum, p) => sum + p.price, 0) / results.length;
      suggestions.push({
        type: 'price',
        text: `Average price is ${Math.round(avgPrice).toLocaleString('en-IN')}. Filter by budget?`,
        icon: ''
      });
    }

    return suggestions.slice(0, 3); // Return top 3 suggestions
  };

  const generateAlternatives = (query) => {
    const synonyms = {
      'phone': ['mobile', 'smartphone', 'cellphone'],
      'laptop': ['notebook', 'computer', 'pc'],
      'headphones': ['earphones', 'earbuds', 'audio'],
      'watch': ['timepiece', 'wristwatch', 'smartwatch'],
      'camera': ['photography', 'photo equipment', 'lens']
    };

    const alternatives = [];
    Object.keys(synonyms).forEach(key => {
      if (query.toLowerCase().includes(key)) {
        synonyms[key].forEach(synonym => {
          alternatives.push(query.toLowerCase().replace(key, synonym));
        });
      }
    });

    return alternatives.slice(0, 2);
  };

  const saveSearch = (query, searchFilters) => {
    const savedSearch = {
      id: Date.now(),
      query,
      filters: searchFilters,
      name: `${query} ${searchFilters.category ? `in ${searchFilters.category}` : ''}`.trim(),
      savedAt: new Date()
    };

    const updatedSaved = [savedSearch, ...savedSearches.slice(0, 9)]; // Keep last 10
    setSavedSearches(updatedSaved);
    localStorage.setItem('savedSearches', JSON.stringify(updatedSaved));
  };

  const deleteSavedSearch = (searchId) => {
    const updatedSaved = savedSearches.filter(search => search.id !== searchId);
    setSavedSearches(updatedSaved);
    localStorage.setItem('savedSearches', JSON.stringify(updatedSaved));
  };

  const getSearchSuggestions = (partialQuery) => {
    if (!partialQuery || partialQuery.length < 2) return [];

    // Combine popular searches, search history, and product titles
    const allSuggestions = [
      ...popularSearches.map(s => ({ text: s.query, type: 'popular' })),
      ...searchHistory.map(s => ({ text: s.query, type: 'history' })),
      ...products.map(p => ({ text: p.title, type: 'product' }))
    ];

    return allSuggestions
      .filter(suggestion => 
        suggestion.text.toLowerCase().includes(partialQuery.toLowerCase())
      )
      .slice(0, 8)
      .map(suggestion => ({
        ...suggestion,
        highlight: highlightMatch(suggestion.text, partialQuery)
      }));
  };

  const highlightMatch = (text, query) => {
    const regex = new RegExp(`(${query})`, 'gi');
    return text.replace(regex, '<mark>$1</mark>');
  };

  const clearSearchHistory = () => {
    setSearchHistory([]);
    localStorage.removeItem('searchHistory');
  };

  const getFilterOptions = () => {
    // Extract unique values from products for filter options
    const categories = [...new Set(products.map(p => p.category))];
    const brands = [...new Set(products.map(p => p.brand).filter(Boolean))];
    const maxPrice = Math.max(...products.map(p => p.price));

    return {
      categories: categories.map(cat => ({ value: cat, label: cat })),
      brands: brands.map(brand => ({ value: brand, label: brand })),
      priceRange: { min: 0, max: Math.ceil(maxPrice / 1000) * 1000 },
      ratings: [1, 2, 3, 4, 5],
      sortOptions: [
        { value: 'relevance', label: 'Relevance' },
        { value: 'price-low', label: 'Price: Low to High' },
        { value: 'price-high', label: 'Price: High to Low' },
        { value: 'rating', label: 'Customer Rating' },
        { value: 'newest', label: 'Newest Arrivals' },
        { value: 'popularity', label: 'Most Popular' },
        { value: 'discount', label: 'Highest Discount' }
      ]
    };
  };

  const updateFilters = (newFilters) => {
    setFilters(prev => ({ ...prev, ...newFilters }));
  };

  const resetFilters = () => {
    setFilters({
      category: '',
      brand: '',
      priceRange: { min: 0, max: 100000 },
      rating: 0,
      availability: 'all',
      shipping: 'all',
      seller: 'all',
      condition: 'all',
      sortBy: 'relevance',
      discountPercentage: 0,
      location: '',
      features: []
    });
  };

  const value = {
    // Search functions
    performAdvancedSearch,
    getSearchSuggestions,
    
    // Search management
    saveSearch,
    deleteSavedSearch,
    clearSearchHistory,
    
    // Filters
    filters,
    updateFilters,
    resetFilters,
    getFilterOptions,
    
    // Data
    searchHistory,
    savedSearches,
    searchSuggestions,
    popularSearches,
    aiInsights,
    
    // State
    isLoading
  };

  return (
    <AdvancedSearchContext.Provider value={value}>
      {children}
    </AdvancedSearchContext.Provider>
  );
};

export default AdvancedSearchContext;