import React, { createContext, useContext, useState, useEffect } from 'react';
import { useAuth } from './AuthContext';

const AnalyticsContext = createContext();

export const useAnalytics = () => {
  const context = useContext(AnalyticsContext);
  if (!context) {
    throw new Error('useAnalytics must be used within an AnalyticsProvider');
  }
  return context;
};

export const AnalyticsProvider = ({ children }) => {
  const { user, isAuthenticated } = useAuth();
  
  const [userBehavior, setUserBehavior] = useState({});
  const [productAnalytics, setProductAnalytics] = useState({});
  const [personalizedData, setPersonalizedData] = useState({});
  const [isLoading, setIsLoading] = useState(false);

  // Sample analytics data
  const sampleUserBehavior = {
    1: {
      userId: 1,
      sessionData: {
        totalSessions: 45,
        averageSessionDuration: 12.5, // minutes
        bounceRate: 0.32,
        pagesPerSession: 5.8,
        lastVisit: new Date(),
        deviceType: 'desktop',
        browser: 'Chrome',
        location: 'New York, USA'
      },
      browsingHistory: [
        {
          productId: 1,
          productName: "iPhone 15 Pro Max",
          category: "Electronics",
          viewedAt: new Date(Date.now() - 2 * 60 * 60 * 1000),
          timeSpent: 4.2, // minutes
          actionsPerformed: ['view', 'add_to_cart', 'view_reviews']
        },
        {
          productId: 2,
          productName: "Samsung Galaxy S24 Ultra",
          category: "Electronics",
          viewedAt: new Date(Date.now() - 5 * 60 * 60 * 1000),
          timeSpent: 2.8,
          actionsPerformed: ['view', 'compare', 'add_to_wishlist']
        },
        {
          productId: 3,
          productName: "MacBook Pro M3",
          category: "Electronics",
          viewedAt: new Date(Date.now() - 24 * 60 * 60 * 1000),
          timeSpent: 8.5,
          actionsPerformed: ['view', 'view_reviews', 'ask_question', 'price_alert']
        }
      ],
      searchHistory: [
        {
          query: "best smartphone 2024",
          resultsClicked: 3,
          searchedAt: new Date(Date.now() - 6 * 60 * 60 * 1000),
          filters: ['Electronics', 'price_range_high']
        },
        {
          query: "laptop for programming",
          resultsClicked: 2,
          searchedAt: new Date(Date.now() - 24 * 60 * 60 * 1000),
          filters: ['Electronics', 'brand_apple', 'brand_dell']
        }
      ],
      categoryPreferences: {
        'Electronics': { views: 25, purchases: 8, timeSpent: 180 },
        'Home & Garden': { views: 12, purchases: 3, timeSpent: 45 },
        'Books': { views: 8, purchases: 5, timeSpent: 30 },
        'Sports': { views: 5, purchases: 2, timeSpent: 20 }
      },
      priceRangePreferences: {
        'budget': { min: 0, max: 5000, frequency: 0.2 },
        'mid_range': { min: 5000, max: 25000, frequency: 0.5 },
        'premium': { min: 25000, max: 100000, frequency: 0.25 },
        'luxury': { min: 100000, max: 500000, frequency: 0.05 }
      },
      purchasePatterns: {
        averageOrderValue: 28500,
        purchaseFrequency: 2.3, // purchases per month
        seasonalTrends: {
          'Q1': 0.15,
          'Q2': 0.25,
          'Q3': 0.35,
          'Q4': 0.25 // Holiday season
        },
        dayOfWeekPreference: {
          'Monday': 0.10,
          'Tuesday': 0.12,
          'Wednesday': 0.15,
          'Thursday': 0.18,
          'Friday': 0.20,
          'Saturday': 0.15,
          'Sunday': 0.10
        },
        timeOfDayPreference: {
          'morning': 0.20,
          'afternoon': 0.35,
          'evening': 0.30,
          'night': 0.15
        }
      }
    }
  };

  const sampleProductAnalytics = {
    1: {
      productId: 1,
      views: 1250,
      uniqueViews: 890,
      averageTimeOnPage: 3.5,
      bounceRate: 0.28,
      conversionRate: 0.045,
      addToCartRate: 0.12,
      wishlistRate: 0.08,
      reviewRate: 0.034,
      shareRate: 0.015,
      demographics: {
        ageGroups: {
          '18-24': 0.25,
          '25-34': 0.35,
          '35-44': 0.25,
          '45-54': 0.10,
          '55+': 0.05
        },
        gender: {
          'male': 0.65,
          'female': 0.32,
          'other': 0.03
        },
        locations: [
          { city: 'Mumbai', percentage: 0.22 },
          { city: 'Delhi', percentage: 0.18 },
          { city: 'Bangalore', percentage: 0.15 },
          { city: 'Chennai', percentage: 0.12 },
          { city: 'Others', percentage: 0.33 }
        ]
      },
      trending: {
        isRising: true,
        growthRate: 0.15, // 15% increase
        trendingRank: 3,
        category: 'Electronics'
      }
    }
  };

  useEffect(() => {
    // Load analytics data
    const savedBehavior = localStorage.getItem('userBehavior');
    const savedProductAnalytics = localStorage.getItem('productAnalytics');

    if (savedBehavior) {
      setUserBehavior(JSON.parse(savedBehavior));
    } else {
      setUserBehavior(sampleUserBehavior);
      localStorage.setItem('userBehavior', JSON.stringify(sampleUserBehavior));
    }

    if (savedProductAnalytics) {
      setProductAnalytics(JSON.parse(savedProductAnalytics));
    } else {
      setProductAnalytics(sampleProductAnalytics);
      localStorage.setItem('productAnalytics', JSON.stringify(sampleProductAnalytics));
    }
  }, []);

  const trackEvent = (eventType, eventData) => {
    // Track user events for analytics
    const timestamp = new Date();
    
    if (!isAuthenticated || !user) {
      // Track anonymous events
      console.log('Anonymous event tracked:', { eventType, eventData, timestamp });
      return;
    }

    const currentBehavior = userBehavior[user.id] || {
      userId: user.id,
      sessionData: {},
      browsingHistory: [],
      searchHistory: [],
      categoryPreferences: {},
      priceRangePreferences: {},
      purchasePatterns: {}
    };

    // Update user behavior based on event type
    let updatedBehavior = { ...currentBehavior };

    switch (eventType) {
      case 'product_view':
        updatedBehavior.browsingHistory = [
          {
            productId: eventData.productId,
            productName: eventData.productName,
            category: eventData.category,
            viewedAt: timestamp,
            timeSpent: eventData.timeSpent || 0,
            actionsPerformed: ['view']
          },
          ...currentBehavior.browsingHistory.slice(0, 49) // Keep last 50 views
        ];
        break;

      case 'search':
        updatedBehavior.searchHistory = [
          {
            query: eventData.query,
            resultsClicked: eventData.resultsClicked || 0,
            searchedAt: timestamp,
            filters: eventData.filters || []
          },
          ...currentBehavior.searchHistory.slice(0, 29) // Keep last 30 searches
        ];
        break;

      case 'add_to_cart':
      case 'add_to_wishlist':
      case 'purchase':
        // Update category preferences
        const category = eventData.category;
        if (category) {
          updatedBehavior.categoryPreferences = {
            ...currentBehavior.categoryPreferences,
            [category]: {
              ...currentBehavior.categoryPreferences[category],
              [eventType === 'product_view' ? 'views' : eventType === 'purchase' ? 'purchases' : 'interactions']: 
                (currentBehavior.categoryPreferences[category]?.[eventType === 'product_view' ? 'views' : eventType === 'purchase' ? 'purchases' : 'interactions'] || 0) + 1
            }
          };
        }
        break;
    }

    const newUserBehavior = {
      ...userBehavior,
      [user.id]: updatedBehavior
    };

    setUserBehavior(newUserBehavior);
    localStorage.setItem('userBehavior', JSON.stringify(newUserBehavior));
  };

  const getPersonalizedRecommendations = () => {
    if (!isAuthenticated || !user) return [];

    const behavior = userBehavior[user.id];
    if (!behavior) return [];

    // Analyze user preferences and generate recommendations
    const categoryPrefs = behavior.categoryPreferences || {};
    const browsingHistory = behavior.browsingHistory || [];
    
    // Get top categories by engagement
    const topCategories = Object.entries(categoryPrefs)
      .sort(([,a], [,b]) => (b.views + b.purchases * 3) - (a.views + a.purchases * 3))
      .slice(0, 3)
      .map(([category]) => category);

    // Get recently viewed products
    const recentlyViewed = browsingHistory.slice(0, 5);

    // Generate recommendations based on patterns
    const recommendations = {
      topCategories,
      recentlyViewed,
      suggestedProducts: [], // Would be populated by ML algorithm
      priceRange: behavior.priceRangePreferences,
      bestTimeToShow: getBestEngagementTime(behavior)
    };

    return recommendations;
  };

  const getBestEngagementTime = (behavior) => {
    const patterns = behavior.purchasePatterns?.timeOfDayPreference || {};
    const bestTime = Object.entries(patterns)
      .sort(([,a], [,b]) => b - a)[0];
    
    return bestTime ? bestTime[0] : 'evening';
  };

  const getUserSegment = () => {
    if (!isAuthenticated || !user) return 'anonymous';

    const behavior = userBehavior[user.id];
    if (!behavior) return 'new_user';

    const purchasePatterns = behavior.purchasePatterns || {};
    const sessionData = behavior.sessionData || {};
    
    // Segment based on engagement and purchase behavior
    if (purchasePatterns.purchaseFrequency > 3) {
      return 'high_value';
    } else if (purchasePatterns.purchaseFrequency > 1) {
      return 'regular';
    } else if (sessionData.totalSessions > 10) {
      return 'browser';
    } else {
      return 'new_user';
    }
  };

  const getProductInsights = (productId) => {
    const analytics = productAnalytics[productId];
    if (!analytics) return null;

    return {
      performanceScore: calculatePerformanceScore(analytics),
      popularityTrend: analytics.trending,
      conversionOptimization: {
        conversionRate: analytics.conversionRate,
        recommendations: getConversionRecommendations(analytics)
      },
      audienceInsights: analytics.demographics
    };
  };

  const calculatePerformanceScore = (analytics) => {
    const conversionWeight = 0.4;
    const engagementWeight = 0.3;
    const popularityWeight = 0.3;

    const conversionScore = Math.min(analytics.conversionRate * 100, 10) * 10; // Max 100
    const engagementScore = (1 - analytics.bounceRate) * 100;
    const popularityScore = Math.min(analytics.views / 100, 100); // Normalize views

    return Math.round(
      conversionScore * conversionWeight +
      engagementScore * engagementWeight +
      popularityScore * popularityWeight
    );
  };

  const getConversionRecommendations = (analytics) => {
    const recommendations = [];

    if (analytics.bounceRate > 0.5) {
      recommendations.push("High bounce rate - consider improving product images and descriptions");
    }

    if (analytics.addToCartRate < 0.1) {
      recommendations.push("Low add-to-cart rate - review pricing and product positioning");
    }

    if (analytics.conversionRate < 0.03) {
      recommendations.push("Low conversion rate - optimize checkout process and build trust signals");
    }

    return recommendations;
  };

  const getMarketTrends = () => {
    // Analyze overall market trends
    const trendingProducts = Object.values(productAnalytics)
      .filter(p => p.trending?.isRising)
      .sort((a, b) => b.trending.growthRate - a.trending.growthRate)
      .slice(0, 10);

    const categoryTrends = {};
    Object.values(productAnalytics).forEach(product => {
      const category = product.category || 'Other';
      if (!categoryTrends[category]) {
        categoryTrends[category] = {
          totalViews: 0,
          averageConversion: 0,
          productCount: 0
        };
      }
      categoryTrends[category].totalViews += product.views;
      categoryTrends[category].averageConversion += product.conversionRate;
      categoryTrends[category].productCount += 1;
    });

    // Calculate averages
    Object.keys(categoryTrends).forEach(category => {
      categoryTrends[category].averageConversion /= categoryTrends[category].productCount;
    });

    return {
      trendingProducts,
      categoryTrends,
      seasonalInsights: getSeasonalInsights()
    };
  };

  const getSeasonalInsights = () => {
    const currentMonth = new Date().getMonth();
    const seasons = {
      'winter': [11, 0, 1],
      'spring': [2, 3, 4],
      'summer': [5, 6, 7],
      'autumn': [8, 9, 10]
    };

    const currentSeason = Object.entries(seasons)
      .find(([season, months]) => months.includes(currentMonth))?.[0] || 'summer';

    return {
      currentSeason,
      recommendations: getSeasonalRecommendations(currentSeason)
    };
  };

  const getSeasonalRecommendations = (season) => {
    const recommendations = {
      'winter': ['Electronics', 'Home & Garden', 'Fashion'],
      'spring': ['Home & Garden', 'Sports', 'Books'],
      'summer': ['Sports', 'Travel', 'Electronics'],
      'autumn': ['Electronics', 'Books', 'Fashion']
    };

    return recommendations[season] || [];
  };

  const value = {
    userBehavior,
    productAnalytics,
    personalizedData,
    isLoading,
    trackEvent,
    getPersonalizedRecommendations,
    getUserSegment,
    getProductInsights,
    getMarketTrends
  };

  return (
    <AnalyticsContext.Provider value={value}>
      {children}
    </AnalyticsContext.Provider>
  );
};

export default AnalyticsContext;