import React, { createContext, useContext, useReducer, useEffect } from 'react';

const CartContext = createContext();

// Cart action types
const CART_ACTIONS = {
  ADD_TO_CART: 'ADD_TO_CART',
  REMOVE_FROM_CART: 'REMOVE_FROM_CART',
  UPDATE_QUANTITY: 'UPDATE_QUANTITY',
  CLEAR_CART: 'CLEAR_CART',
  LOAD_CART: 'LOAD_CART',
  TOGGLE_CART_SIDEBAR: 'TOGGLE_CART_SIDEBAR',
  OPEN_CART_SIDEBAR: 'OPEN_CART_SIDEBAR',
  CLOSE_CART_SIDEBAR: 'CLOSE_CART_SIDEBAR'
};

// Cart reducer
const cartReducer = (state, action) => {
  switch (action.type) {
    case CART_ACTIONS.ADD_TO_CART: {
      const { product, quantity = 1 } = action.payload;
      const existingItem = state.items.find(item => item.id === product.id);
      
      if (existingItem) {
        return {
          ...state,
          items: state.items.map(item =>
            item.id === product.id
              ? { ...item, quantity: item.quantity + quantity }
              : item
          )
        };
      }
      
      return {
        ...state,
        items: [...state.items, { ...product, quantity }]
      };
    }
    
    case CART_ACTIONS.REMOVE_FROM_CART: {
      return {
        ...state,
        items: state.items.filter(item => item.id !== action.payload.productId)
      };
    }
    
    case CART_ACTIONS.UPDATE_QUANTITY: {
      const { productId, quantity } = action.payload;
      
      if (quantity <= 0) {
        return {
          ...state,
          items: state.items.filter(item => item.id !== productId)
        };
      }
      
      return {
        ...state,
        items: state.items.map(item =>
          item.id === productId
            ? { ...item, quantity }
            : item
        )
      };
    }
    
    case CART_ACTIONS.CLEAR_CART: {
      return {
        ...state,
        items: []
      };
    }
    
    case CART_ACTIONS.LOAD_CART: {
      return {
        ...state,
        items: action.payload.items || []
      };
    }
    
    case CART_ACTIONS.TOGGLE_CART_SIDEBAR: {
      return {
        ...state,
        isCartSidebarOpen: !state.isCartSidebarOpen
      };
    }
    
    case CART_ACTIONS.OPEN_CART_SIDEBAR: {
      return {
        ...state,
        isCartSidebarOpen: true
      };
    }
    
    case CART_ACTIONS.CLOSE_CART_SIDEBAR: {
      return {
        ...state,
        isCartSidebarOpen: false
      };
    }
    
    default:
      return state;
  }
};

// Initial cart state
const initialCartState = {
  items: [],
  isCartSidebarOpen: false
};

// Cart Provider Component
export const CartProvider = ({ children }) => {
  const [cartState, dispatch] = useReducer(cartReducer, initialCartState);
  
  // Load cart from localStorage on mount
  useEffect(() => {
    const savedCart = localStorage.getItem('amazon-clone-cart');
    if (savedCart) {
      try {
        const parsedCart = JSON.parse(savedCart);
        dispatch({
          type: CART_ACTIONS.LOAD_CART,
          payload: { items: parsedCart }
        });
      } catch (error) {
        console.error('Error loading cart from localStorage:', error);
      }
    }
  }, []);
  
  // Save cart to localStorage whenever it changes
  useEffect(() => {
    localStorage.setItem('amazon-clone-cart', JSON.stringify(cartState.items));
  }, [cartState.items]);
  
  // Cart utility functions
  const addToCart = (product, quantity = 1) => {
    dispatch({
      type: CART_ACTIONS.ADD_TO_CART,
      payload: { product, quantity }
    });
  };
  
  const removeFromCart = (productId) => {
    dispatch({
      type: CART_ACTIONS.REMOVE_FROM_CART,
      payload: { productId }
    });
  };
  
  const updateQuantity = (productId, quantity) => {
    dispatch({
      type: CART_ACTIONS.UPDATE_QUANTITY,
      payload: { productId, quantity }
    });
  };
  
  const clearCart = () => {
    dispatch({ type: CART_ACTIONS.CLEAR_CART });
  };
  
  const getCartTotal = () => {
    return cartState.items.reduce((total, item) => total + (item.price * item.quantity), 0);
  };
  
  const getCartItemCount = () => {
    return cartState.items.reduce((count, item) => count + item.quantity, 0);
  };
  
  const isInCart = (productId) => {
    return cartState.items.some(item => item.id === productId);
  };
  
  const getCartItem = (productId) => {
    return cartState.items.find(item => item.id === productId);
  };
  
  const toggleCartSidebar = () => {
    dispatch({ type: CART_ACTIONS.TOGGLE_CART_SIDEBAR });
  };
  
  const openCartSidebar = () => {
    dispatch({ type: CART_ACTIONS.OPEN_CART_SIDEBAR });
  };
  
  const closeCartSidebar = () => {
    dispatch({ type: CART_ACTIONS.CLOSE_CART_SIDEBAR });
  };
  
  const value = {
    cartItems: cartState.items,
    cart: cartState.items, // Keep for backward compatibility
    isCartSidebarOpen: cartState.isCartSidebarOpen,
    addToCart,
    removeFromCart,
    updateQuantity,
    clearCart,
    getCartTotal,
    getCartItemCount,
    isInCart,
    getCartItem,
    toggleCartSidebar,
    openCartSidebar,
    closeCartSidebar
  };
  
  return (
    <CartContext.Provider value={value}>
      {children}
    </CartContext.Provider>
  );
};

// Custom hook to use cart context
export const useCart = () => {
  const context = useContext(CartContext);
  if (!context) {
    throw new Error('useCart must be used within a CartProvider');
  }
  return context;
};

export default CartContext;