import React, { createContext, useContext, useState, useEffect } from 'react';
import { useProducts } from './ProductContext';

const SearchContext = createContext();

export const useSearch = () => {
  const context = useContext(SearchContext);
  if (!context) {
    throw new Error('useSearch must be used within a SearchProvider');
  }
  return context;
};

export const SearchProvider = ({ children }) => {
  const { products } = useProducts();
  const [searchQuery, setSearchQuery] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [filters, setFilters] = useState({
    category: '',
    priceRange: { min: 0, max: 300000 },
    rating: 0,
    brand: '',
    features: [],
    inStock: false,
    freeShipping: false,
    isPrime: false,
    sortBy: 'relevance'
  });
  const [searchHistory, setSearchHistory] = useState([]);
  const [searchSuggestions, setSearchSuggestions] = useState([]);
  const [isSearching, setIsSearching] = useState(false);
  const [showSuggestions, setShowSuggestions] = useState(false);

  // Load search history from localStorage
  useEffect(() => {
    const savedHistory = localStorage.getItem('amazon_search_history');
    if (savedHistory) {
      try {
        setSearchHistory(JSON.parse(savedHistory));
      } catch (error) {
        console.error('Error parsing search history:', error);
      }
    }
  }, []);

  // Save search history to localStorage
  useEffect(() => {
    if (searchHistory.length > 0) {
      localStorage.setItem('amazon_search_history', JSON.stringify(searchHistory));
    }
  }, [searchHistory]);

  const performSearch = async (query, customFilters = {}) => {
    if (!query.trim()) {
      setSearchResults([]);
      return;
    }

    setIsSearching(true);
    const searchTerm = query.toLowerCase();
    const activeFilters = { ...filters, ...customFilters };

    try {
      // Simulate API delay
      await new Promise(resolve => setTimeout(resolve, 300));

      let results = products.filter(product => {
        // Text search in name, category, description
        const matchesQuery = 
          product.name.toLowerCase().includes(searchTerm) ||
          product.category.toLowerCase().includes(searchTerm) ||
          (product.description && product.description.toLowerCase().includes(searchTerm)) ||
          (product.tags && product.tags.some(tag => tag.toLowerCase().includes(searchTerm)));

        // Category filter
        const matchesCategory = !activeFilters.category || 
          product.category.toLowerCase() === activeFilters.category.toLowerCase();

        // Price range filter
        const matchesPrice = 
          product.price >= activeFilters.priceRange.min && 
          product.price <= activeFilters.priceRange.max;

        // Rating filter
        const matchesRating = !activeFilters.rating || 
          (product.rating >= activeFilters.rating);

        return matchesQuery && matchesCategory && matchesPrice && matchesRating;
      });

      // Sort results
      switch (activeFilters.sortBy) {
        case 'price_low':
          results.sort((a, b) => a.price - b.price);
          break;
        case 'price_high':
          results.sort((a, b) => b.price - a.price);
          break;
        case 'rating':
          results.sort((a, b) => (b.rating || 0) - (a.rating || 0));
          break;
        case 'newest':
          results.sort((a, b) => new Date(b.createdAt || 0) - new Date(a.createdAt || 0));
          break;
        case 'relevance':
        default:
          // Keep original order for relevance
          break;
      }

      setSearchResults(results);
      
      // Add to search history if not already present
      if (query.trim() && !searchHistory.includes(query.trim())) {
        setSearchHistory(prev => [query.trim(), ...prev.slice(0, 9)]); // Keep last 10 searches
      }

    } catch (error) {
      console.error('Search error:', error);
      setSearchResults([]);
    } finally {
      setIsSearching(false);
    }
  };

  const updateFilters = (newFilters) => {
    setFilters(prev => ({ ...prev, ...newFilters }));
  };

  const clearSearch = () => {
    setSearchQuery('');
    setSearchResults([]);
  };

  const clearSearchHistory = () => {
    setSearchHistory([]);
    localStorage.removeItem('amazon_search_history');
  };

  const removeFromHistory = (query) => {
    const updatedHistory = searchHistory.filter(item => item !== query);
    setSearchHistory(updatedHistory);
  };

  const getCategories = () => {
    const categories = [...new Set(products.map(product => product.category))];
    return categories.sort();
  };

  const getSuggestions = (query) => {
    if (!query.trim()) return [];
    
    const suggestions = [];
    const searchTerm = query.toLowerCase();

    // Product name suggestions
    products.forEach(product => {
      if (product.name.toLowerCase().includes(searchTerm)) {
        suggestions.push({
          type: 'product',
          value: product.name,
          product: product
        });
      }
    });

    // Category suggestions
    const categories = getCategories();
    categories.forEach(category => {
      if (category.toLowerCase().includes(searchTerm)) {
        suggestions.push({
          type: 'category',
          value: category,
          count: products.filter(p => p.category === category).length
        });
      }
    });

    // Remove duplicates and limit results
    const uniqueSuggestions = suggestions.filter((suggestion, index, self) => 
      index === self.findIndex(s => s.value === suggestion.value)
    );

    return uniqueSuggestions.slice(0, 8);
  };

  const value = {
    searchQuery,
    setSearchQuery,
    searchResults,
    filters,
    updateFilters,
    searchHistory,
    isSearching,
    performSearch,
    clearSearch,
    clearSearchHistory,
    removeFromHistory,
    getCategories,
    getSuggestions
  };

  return (
    <SearchContext.Provider value={value}>
      {children}
    </SearchContext.Provider>
  );
};

export default SearchContext;