import { useState, useEffect, useCallback } from 'react';
import { useAuth } from '../context/AuthContext';
import { useProducts } from '../context/ProductContext';
import recommendationEngine from '../services/recommendationEngine';

// Custom hook for AI-powered recommendations
export const useRecommendations = () => {
  const { user, isAuthenticated } = useAuth();
  const { products } = useProducts();
  const [recommendations, setRecommendations] = useState({
    personal: [],
    trending: [],
    related: [],
    crossSell: [],
    loading: false,
    error: null
  });

  // Track user behavior
  const trackBehavior = useCallback((action, productId, metadata = {}) => {
    if (!isAuthenticated || !user?.id) {
      // For anonymous users, use session-based tracking
      const sessionId = localStorage.getItem('sessionId') || 
                       `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
      localStorage.setItem('sessionId', sessionId);
      recommendationEngine.trackUserBehavior(sessionId, action, productId, metadata);
    } else {
      recommendationEngine.trackUserBehavior(user.id, action, productId, metadata);
    }
  }, [isAuthenticated, user]);

  // Get personalized recommendations
  const getPersonalRecommendations = useCallback(async (limit = 10) => {
    if (!products.length) return [];
    
    setRecommendations(prev => ({ ...prev, loading: true }));
    
    try {
      const userId = isAuthenticated && user?.id ? 
                    user.id : 
                    localStorage.getItem('sessionId');
      
      if (!userId) {
        return recommendationEngine.getTrendingRecommendations(products, limit);
      }

      const personal = recommendationEngine.getHybridRecommendations(userId, products, limit);
      return personal;
    } catch (error) {
      console.error('Error getting personal recommendations:', error);
      return recommendationEngine.getTrendingRecommendations(products, limit);
    } finally {
      setRecommendations(prev => ({ ...prev, loading: false }));
    }
  }, [products, isAuthenticated, user]);

  // Get trending recommendations
  const getTrendingRecommendations = useCallback((limit = 10) => {
    if (!products.length) return [];
    return recommendationEngine.getTrendingRecommendations(products, limit);
  }, [products]);

  // Get related products for a specific product
  const getRelatedProducts = useCallback((productId, limit = 4) => {
    if (!products.length) return [];
    return recommendationEngine.getRelatedProducts(productId, products, limit);
  }, [products]);

  // Get cross-sell recommendations for cart
  const getCrossSellRecommendations = useCallback((cartItems, limit = 6) => {
    if (!products.length || !cartItems.length) return [];
    return recommendationEngine.getCrossSellRecommendations(cartItems, products, limit);
  }, [products]);

  // Load initial recommendations
  useEffect(() => {
    const loadRecommendations = async () => {
      if (!products.length) return;

      setRecommendations(prev => ({ ...prev, loading: true, error: null }));

      try {
        const [personal, trending] = await Promise.all([
          getPersonalRecommendations(12),
          Promise.resolve(getTrendingRecommendations(8))
        ]);

        setRecommendations(prev => ({
          ...prev,
          personal,
          trending,
          loading: false
        }));
      } catch (error) {
        console.error('Error loading recommendations:', error);
        setRecommendations(prev => ({
          ...prev,
          error: error.message,
          loading: false
        }));
      }
    };

    loadRecommendations();
  }, [products, getPersonalRecommendations, getTrendingRecommendations]);

  return {
    recommendations,
    trackBehavior,
    getPersonalRecommendations,
    getTrendingRecommendations,
    getRelatedProducts,
    getCrossSellRecommendations,
    refreshRecommendations: () => {
      if (products.length) {
        getPersonalRecommendations(12).then(personal => {
          setRecommendations(prev => ({ ...prev, personal }));
        });
      }
    }
  };
};

// Hook for recommendation analytics
export const useRecommendationAnalytics = () => {
  const [analytics, setAnalytics] = useState({
    clickThroughRate: 0,
    conversionRate: 0,
    recommendationViews: 0,
    recommendationClicks: 0,
    recommendationPurchases: 0
  });

  const trackRecommendationView = useCallback((recommendationType, productIds) => {
    setAnalytics(prev => ({
      ...prev,
      recommendationViews: prev.recommendationViews + 1
    }));
    
    // Store in localStorage for persistence
    const views = parseInt(localStorage.getItem('rec_views') || '0') + 1;
    localStorage.setItem('rec_views', views.toString());
  }, []);

  const trackRecommendationClick = useCallback((recommendationType, productId) => {
    setAnalytics(prev => ({
      ...prev,
      recommendationClicks: prev.recommendationClicks + 1,
      clickThroughRate: prev.recommendationViews > 0 ? 
        (prev.recommendationClicks + 1) / prev.recommendationViews : 0
    }));

    // Store in localStorage for persistence
    const clicks = parseInt(localStorage.getItem('rec_clicks') || '0') + 1;
    localStorage.setItem('rec_clicks', clicks.toString());
  }, []);

  const trackRecommendationPurchase = useCallback((productId) => {
    setAnalytics(prev => ({
      ...prev,
      recommendationPurchases: prev.recommendationPurchases + 1,
      conversionRate: prev.recommendationClicks > 0 ? 
        (prev.recommendationPurchases + 1) / prev.recommendationClicks : 0
    }));

    // Store in localStorage for persistence
    const purchases = parseInt(localStorage.getItem('rec_purchases') || '0') + 1;
    localStorage.setItem('rec_purchases', purchases.toString());
  }, []);

  // Load analytics from localStorage on mount
  useEffect(() => {
    const views = parseInt(localStorage.getItem('rec_views') || '0');
    const clicks = parseInt(localStorage.getItem('rec_clicks') || '0');
    const purchases = parseInt(localStorage.getItem('rec_purchases') || '0');

    setAnalytics({
      recommendationViews: views,
      recommendationClicks: clicks,
      recommendationPurchases: purchases,
      clickThroughRate: views > 0 ? clicks / views : 0,
      conversionRate: clicks > 0 ? purchases / clicks : 0
    });
  }, []);

  return {
    analytics,
    trackRecommendationView,
    trackRecommendationClick,
    trackRecommendationPurchase
  };
};