import React, { createContext, useContext, useState, useEffect } from 'react';
import { useAuth } from './AuthContext';
import { useProducts } from './ProductContext';
import { useAnalytics } from './AnalyticsContext';
import { useCart } from './CartContext';
import { useWishlist } from './WishlistContext';

const RecommendationContext = createContext();

export const useRecommendation = () => {
  const context = useContext(RecommendationContext);
  if (!context) {
    throw new Error('useRecommendation must be used within a RecommendationProvider');
  }
  return context;
};

export const RecommendationProvider = ({ children }) => {
  const { user, isAuthenticated } = useAuth();
  const { products } = useProducts();
  const { trackEvent } = useAnalytics();
  const { cart } = useCart();
  const { wishlistItems } = useWishlist();

  const [personalizedRecommendations, setPersonalizedRecommendations] = useState([]);
  const [trendingProducts, setTrendingProducts] = useState([]);
  const [similarProducts, setSimilarProducts] = useState({});
  const [frequentlyBoughtTogether, setFrequentlyBoughtTogether] = useState({});
  const [recentlyViewed, setRecentlyViewed] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  // User preferences based on interaction history
  const [userPreferences, setUserPreferences] = useState({
    categories: {},
    brands: {},
    priceRange: { min: 0, max: 100000 },
    features: [],
    colors: [],
    styles: []
  });

  // Recommendation algorithms
  const algorithms = {
    COLLABORATIVE_FILTERING: 'collaborative',
    CONTENT_BASED: 'content',
    HYBRID: 'hybrid',
    TRENDING: 'trending',
    SEASONAL: 'seasonal'
  };

  useEffect(() => {
    // Load user preferences and recommendations from localStorage
    const savedPreferences = localStorage.getItem(`userPreferences_${user?.id}`);
    const savedRecommendations = localStorage.getItem(`recommendations_${user?.id}`);
    const savedRecentlyViewed = localStorage.getItem(`recentlyViewed_${user?.id}`);

    if (savedPreferences) {
      setUserPreferences(JSON.parse(savedPreferences));
    }

    if (savedRecommendations) {
      setPersonalizedRecommendations(JSON.parse(savedRecommendations));
    }

    if (savedRecentlyViewed) {
      setRecentlyViewed(JSON.parse(savedRecentlyViewed));
    }

    // Generate initial recommendations
    generateRecommendations();
    generateTrendingProducts();
  }, [user, products]);

  const generateRecommendations = async () => {
    if (!isAuthenticated || products.length === 0) return;

    setIsLoading(true);

    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));

      // Get user interaction data
      const userInteractions = {
        cartItems: cart,
        wishlistItems: wishlistItems,
        recentlyViewed,
        purchaseHistory: [], // In real app, this would come from orders
        searchHistory: JSON.parse(localStorage.getItem('searchHistory') || '[]')
      };

      // Apply multiple recommendation algorithms
      const collaborativeRecs = await generateCollaborativeRecommendations(userInteractions);
      const contentBasedRecs = await generateContentBasedRecommendations(userInteractions);
      const trendingRecs = await generateTrendingRecommendations();
      const seasonalRecs = await generateSeasonalRecommendations();

      // Combine and weight recommendations
      const combinedRecommendations = combineRecommendations([
        { type: 'collaborative', recommendations: collaborativeRecs, weight: 0.3 },
        { type: 'content', recommendations: contentBasedRecs, weight: 0.4 },
        { type: 'trending', recommendations: trendingRecs, weight: 0.2 },
        { type: 'seasonal', recommendations: seasonalRecs, weight: 0.1 }
      ]);

      setPersonalizedRecommendations(combinedRecommendations.slice(0, 20));
      
      // Cache recommendations
      localStorage.setItem(
        `recommendations_${user.id}`,
        JSON.stringify(combinedRecommendations.slice(0, 20))
      );

      // Update user preferences based on recommendations
      updateUserPreferences(userInteractions);

    } catch (error) {
      console.error('Error generating recommendations:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const generateCollaborativeRecommendations = async (userInteractions) => {
    // Simulate collaborative filtering algorithm
    // In real implementation, this would use user-item interaction matrix
    
    const userCategories = [
      ...new Set([
        ...userInteractions.cartItems.map(item => item.category),
        ...userInteractions.wishlistItems.map(item => item.category),
        ...userInteractions.recentlyViewed.map(item => item.category)
      ])
    ];

    return products
      .filter(product => 
        userCategories.includes(product.category) &&
        !userInteractions.cartItems.some(item => item.id === product.id) &&
        !userInteractions.wishlistItems.some(item => item.id === product.id)
      )
      .sort((a, b) => b.rating - a.rating)
      .slice(0, 10)
      .map(product => ({
        ...product,
        recommendationType: 'collaborative',
        confidence: Math.random() * 0.3 + 0.7, // 0.7-1.0
        reason: 'Customers with similar preferences also liked this'
      }));
  };

  const generateContentBasedRecommendations = async (userInteractions) => {
    // Content-based filtering using product features
    const userProducts = [
      ...userInteractions.cartItems,
      ...userInteractions.wishlistItems,
      ...userInteractions.recentlyViewed
    ];

    if (userProducts.length === 0) {
      return products.slice(0, 5).map(product => ({
        ...product,
        recommendationType: 'content',
        confidence: 0.6,
        reason: 'Popular products in your area of interest'
      }));
    }

    // Calculate similarity scores
    const recommendedProducts = [];
    
    products.forEach(product => {
      if (userProducts.some(up => up.id === product.id)) return;

      let similarity = 0;
      let matchReasons = [];

      userProducts.forEach(userProduct => {
        // Category similarity
        if (product.category === userProduct.category) {
          similarity += 0.4;
          matchReasons.push(`Similar category: ${product.category}`);
        }

        // Brand similarity
        if (product.brand && userProduct.brand && product.brand === userProduct.brand) {
          similarity += 0.3;
          matchReasons.push(`Same brand: ${product.brand}`);
        }

        // Price range similarity
        const priceDiff = Math.abs(product.price - userProduct.price);
        const maxPrice = Math.max(product.price, userProduct.price);
        if (priceDiff / maxPrice < 0.5) {
          similarity += 0.2;
          matchReasons.push('Similar price range');
        }

        // Rating similarity
        if (Math.abs(product.rating - userProduct.rating) < 1) {
          similarity += 0.1;
        }
      });

      if (similarity > 0.3) {
        recommendedProducts.push({
          ...product,
          recommendationType: 'content',
          confidence: Math.min(similarity, 1),
          reason: matchReasons[0] || 'Based on your preferences',
          similarity
        });
      }
    });

    return recommendedProducts
      .sort((a, b) => b.similarity - a.similarity)
      .slice(0, 10);
  };

  const generateTrendingRecommendations = async () => {
    // Simulate trending algorithm based on rating, reviews, and recency
    return products
      .map(product => ({
        ...product,
        trendingScore: (product.rating * 0.4) + 
                      (Math.log(product.reviewCount || 1) * 0.3) + 
                      (Math.random() * 0.3) // Simulate view count/popularity
      }))
      .sort((a, b) => b.trendingScore - a.trendingScore)
      .slice(0, 10)
      .map(product => ({
        ...product,
        recommendationType: 'trending',
        confidence: 0.8,
        reason: 'Trending now'
      }));
  };

  const generateSeasonalRecommendations = async () => {
    // Simulate seasonal recommendations
    const currentMonth = new Date().getMonth();
    const seasonalCategories = {
      'winter': ['Fashion', 'Home & Living'],
      'spring': ['Sports', 'Home & Garden'],
      'summer': ['Sports', 'Health & Beauty'],
      'autumn': ['Fashion', 'Electronics']
    };

    const currentSeason = currentMonth < 3 ? 'winter' :
                         currentMonth < 6 ? 'spring' :
                         currentMonth < 9 ? 'summer' : 'autumn';

    const relevantCategories = seasonalCategories[currentSeason] || [];

    return products
      .filter(product => relevantCategories.includes(product.category))
      .sort((a, b) => b.rating - a.rating)
      .slice(0, 8)
      .map(product => ({
        ...product,
        recommendationType: 'seasonal',
        confidence: 0.7,
        reason: `Perfect for ${currentSeason}`
      }));
  };

  const combineRecommendations = (algorithmResults) => {
    const productScores = {};

    algorithmResults.forEach(({ recommendations, weight }) => {
      recommendations.forEach((product, index) => {
        const score = (recommendations.length - index) * weight * product.confidence;
        
        if (productScores[product.id]) {
          productScores[product.id].score += score;
          productScores[product.id].reasons.push(product.reason);
        } else {
          productScores[product.id] = {
            ...product,
            score,
            reasons: [product.reason]
          };
        }
      });
    });

    return Object.values(productScores)
      .sort((a, b) => b.score - a.score)
      .map(product => ({
        ...product,
        reason: product.reasons[0], // Use primary reason
        confidence: Math.min(product.score / 10, 1) // Normalize confidence
      }));
  };

  const updateUserPreferences = (userInteractions) => {
    const newPreferences = { ...userPreferences };
    
    // Update category preferences
    const allProducts = [
      ...userInteractions.cartItems,
      ...userInteractions.wishlistItems,
      ...userInteractions.recentlyViewed
    ];

    allProducts.forEach(product => {
      newPreferences.categories[product.category] = 
        (newPreferences.categories[product.category] || 0) + 1;
      
      if (product.brand) {
        newPreferences.brands[product.brand] = 
          (newPreferences.brands[product.brand] || 0) + 1;
      }
    });

    setUserPreferences(newPreferences);
    localStorage.setItem(`userPreferences_${user.id}`, JSON.stringify(newPreferences));
  };

  const getSimilarProducts = async (productId, limit = 6) => {
    if (similarProducts[productId]) {
      return similarProducts[productId];
    }

    const targetProduct = products.find(p => p.id === productId);
    if (!targetProduct) return [];

    const similar = products
      .filter(product => product.id !== productId)
      .map(product => {
        let similarity = 0;

        // Category match
        if (product.category === targetProduct.category) similarity += 0.5;
        
        // Brand match
        if (product.brand && targetProduct.brand && product.brand === targetProduct.brand) {
          similarity += 0.3;
        }

        // Price similarity
        const priceDiff = Math.abs(product.price - targetProduct.price);
        const maxPrice = Math.max(product.price, targetProduct.price);
        similarity += (1 - (priceDiff / maxPrice)) * 0.2;

        return { ...product, similarity };
      })
      .sort((a, b) => b.similarity - a.similarity)
      .slice(0, limit);

    setSimilarProducts(prev => ({ ...prev, [productId]: similar }));
    return similar;
  };

  const getFrequentlyBoughtTogether = async (productId, limit = 3) => {
    if (frequentlyBoughtTogether[productId]) {
      return frequentlyBoughtTogether[productId];
    }

    // Simulate frequently bought together algorithm
    const targetProduct = products.find(p => p.id === productId);
    if (!targetProduct) return [];

    const related = products
      .filter(product => 
        product.id !== productId &&
        (product.category === targetProduct.category || 
         Math.abs(product.price - targetProduct.price) < targetProduct.price * 0.5)
      )
      .sort(() => Math.random() - 0.5) // Simulate co-purchase data
      .slice(0, limit)
      .map(product => ({
        ...product,
        purchaseRate: Math.random() * 0.3 + 0.1 // 10-40% co-purchase rate
      }));

    setFrequentlyBoughtTogether(prev => ({ ...prev, [productId]: related }));
    return related;
  };

  const addToRecentlyViewed = (product) => {
    if (!product || !user) return;

    const updated = [
      product,
      ...recentlyViewed.filter(p => p.id !== product.id)
    ].slice(0, 20); // Keep last 20 viewed products

    setRecentlyViewed(updated);
    localStorage.setItem(`recentlyViewed_${user.id}`, JSON.stringify(updated));

    // Track view event
    trackEvent('product_view', {
      productId: product.id,
      category: product.category,
      price: product.price
    });
  };

  const getRecommendationsForCategory = (category, limit = 10) => {
    return personalizedRecommendations
      .filter(product => product.category === category)
      .slice(0, limit);
  };

  const getRecommendationsByType = (type, limit = 10) => {
    return personalizedRecommendations
      .filter(product => product.recommendationType === type)
      .slice(0, limit);
  };

  const refreshRecommendations = () => {
    generateRecommendations();
  };

  const generateTrendingProducts = () => {
    const trending = products
      .map(product => ({
        ...product,
        trendingScore: (product.rating * 0.4) + 
                      (Math.log(product.reviewCount || 1) * 0.3) + 
                      (Math.random() * 0.3)
      }))
      .sort((a, b) => b.trendingScore - a.trendingScore)
      .slice(0, 15);

    setTrendingProducts(trending);
  };

  const value = {
    // Recommendation data
    personalizedRecommendations,
    trendingProducts,
    recentlyViewed,
    userPreferences,
    
    // Recommendation functions
    getSimilarProducts,
    getFrequentlyBoughtTogether,
    getRecommendationsForCategory,
    getRecommendationsByType,
    
    // User interaction
    addToRecentlyViewed,
    refreshRecommendations,
    
    // State
    isLoading
  };

  return (
    <RecommendationContext.Provider value={value}>
      {children}
    </RecommendationContext.Provider>
  );
};

export default RecommendationContext;