import React, { createContext, useContext, useState, useEffect } from 'react';
import { useAuth } from './AuthContext';
import { useNotification } from './NotificationContext';

const PriceTrackingContext = createContext();

export const usePriceTracking = () => {
  const context = useContext(PriceTrackingContext);
  if (!context) {
    throw new Error('usePriceTracking must be used within a PriceTrackingProvider');
  }
  return context;
};

export const PriceTrackingProvider = ({ children }) => {
  const { user, isAuthenticated } = useAuth();
  const { showNotification } = useNotification();
  
  const [priceAlerts, setPriceAlerts] = useState([]);
  const [priceHistory, setPriceHistory] = useState({});
  const [isLoading, setIsLoading] = useState(false);

  // Sample price history data
  const samplePriceHistory = {
    1: {
      productId: 1,
      productName: "iPhone 15 Pro Max",
      currentPrice: 134999,
      originalPrice: 159999,
      history: [
        { date: new Date(Date.now() - 30 * 24 * 60 * 60 * 1000), price: 159999 },
        { date: new Date(Date.now() - 25 * 24 * 60 * 60 * 1000), price: 149999 },
        { date: new Date(Date.now() - 20 * 24 * 60 * 60 * 1000), price: 144999 },
        { date: new Date(Date.now() - 15 * 24 * 60 * 60 * 1000), price: 139999 },
        { date: new Date(Date.now() - 10 * 24 * 60 * 60 * 1000), price: 137999 },
        { date: new Date(Date.now() - 5 * 24 * 60 * 60 * 1000), price: 134999 },
        { date: new Date(), price: 134999 }
      ],
      lowestPrice: 134999,
      highestPrice: 159999,
      averagePrice: 145714,
      priceDropPercentage: 15.6,
      lastUpdated: new Date()
    },
    2: {
      productId: 2,
      productName: "Samsung Galaxy S24 Ultra",
      currentPrice: 124999,
      originalPrice: 134999,
      history: [
        { date: new Date(Date.now() - 30 * 24 * 60 * 60 * 1000), price: 134999 },
        { date: new Date(Date.now() - 20 * 24 * 60 * 60 * 1000), price: 129999 },
        { date: new Date(Date.now() - 10 * 24 * 60 * 60 * 1000), price: 124999 },
        { date: new Date(), price: 124999 }
      ],
      lowestPrice: 124999,
      highestPrice: 134999,
      averagePrice: 128749,
      priceDropPercentage: 7.4,
      lastUpdated: new Date()
    }
  };

  // Sample price alerts
  const samplePriceAlerts = [
    {
      id: 1,
      userId: 1,
      productId: 1,
      productName: "iPhone 15 Pro Max",
      productImage: "/api/placeholder/300/300",
      targetPrice: 130000,
      currentPrice: 134999,
      isActive: true,
      notificationType: 'email', // email, push, both
      createdAt: new Date(Date.now() - 7 * 24 * 60 * 60 * 1000),
      lastChecked: new Date(),
      priceReached: false
    },
    {
      id: 2,
      userId: 1,
      productId: 3,
      productName: "MacBook Pro M3",
      productImage: "/api/placeholder/300/300",
      targetPrice: 180000,
      currentPrice: 199999,
      isActive: true,
      notificationType: 'both',
      createdAt: new Date(Date.now() - 5 * 24 * 60 * 60 * 1000),
      lastChecked: new Date(),
      priceReached: false
    }
  ];

  useEffect(() => {
    // Load price tracking data from localStorage
    const savedAlerts = localStorage.getItem('priceAlerts');
    const savedHistory = localStorage.getItem('priceHistory');

    if (savedAlerts) {
      setPriceAlerts(JSON.parse(savedAlerts));
    } else {
      setPriceAlerts(samplePriceAlerts);
      localStorage.setItem('priceAlerts', JSON.stringify(samplePriceAlerts));
    }

    if (savedHistory) {
      setPriceHistory(JSON.parse(savedHistory));
    } else {
      setPriceHistory(samplePriceHistory);
      localStorage.setItem('priceHistory', JSON.stringify(samplePriceHistory));
    }

    // Start price monitoring
    const interval = setInterval(() => {
      checkPriceAlerts();
    }, 60000); // Check every minute for demo purposes

    return () => clearInterval(interval);
  }, []);

  const createPriceAlert = async (productId, productName, productImage, targetPrice, notificationType = 'email') => {
    if (!isAuthenticated) {
      showNotification('Please login to create price alerts', 'error');
      return { success: false };
    }

    setIsLoading(true);

    try {
      // Check if alert already exists for this product
      const existingAlert = priceAlerts.find(
        alert => alert.userId === user.id && alert.productId === productId && alert.isActive
      );

      if (existingAlert) {
        showNotification('Price alert already exists for this product', 'error');
        return { success: false, error: 'Alert already exists' };
      }

      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));

      const newAlert = {
        id: Date.now(),
        userId: user.id,
        productId: parseInt(productId),
        productName,
        productImage: productImage || '/api/placeholder/300/300',
        targetPrice: parseInt(targetPrice),
        currentPrice: getCurrentProductPrice(productId),
        isActive: true,
        notificationType,
        createdAt: new Date(),
        lastChecked: new Date(),
        priceReached: false
      };

      const updatedAlerts = [...priceAlerts, newAlert];
      setPriceAlerts(updatedAlerts);
      localStorage.setItem('priceAlerts', JSON.stringify(updatedAlerts));

      showNotification('Price alert created successfully!', 'success');
      return { success: true, alert: newAlert };
    } catch (error) {
      showNotification('Failed to create price alert', 'error');
      return { success: false, error: error.message };
    } finally {
      setIsLoading(false);
    }
  };

  const updatePriceAlert = async (alertId, updates) => {
    if (!isAuthenticated) {
      showNotification('Please login to update price alerts', 'error');
      return { success: false };
    }

    setIsLoading(true);

    try {
      await new Promise(resolve => setTimeout(resolve, 500));

      const updatedAlerts = priceAlerts.map(alert =>
        alert.id === alertId ? { ...alert, ...updates, updatedAt: new Date() } : alert
      );

      setPriceAlerts(updatedAlerts);
      localStorage.setItem('priceAlerts', JSON.stringify(updatedAlerts));

      showNotification('Price alert updated successfully!', 'success');
      return { success: true };
    } catch (error) {
      showNotification('Failed to update price alert', 'error');
      return { success: false, error: error.message };
    } finally {
      setIsLoading(false);
    }
  };

  const deletePriceAlert = async (alertId) => {
    if (!isAuthenticated) {
      showNotification('Please login to delete price alerts', 'error');
      return { success: false };
    }

    setIsLoading(true);

    try {
      await new Promise(resolve => setTimeout(resolve, 500));

      const updatedAlerts = priceAlerts.filter(alert => alert.id !== alertId);
      setPriceAlerts(updatedAlerts);
      localStorage.setItem('priceAlerts', JSON.stringify(updatedAlerts));

      showNotification('Price alert deleted successfully!', 'success');
      return { success: true };
    } catch (error) {
      showNotification('Failed to delete price alert', 'error');
      return { success: false, error: error.message };
    } finally {
      setIsLoading(false);
    }
  };

  const getUserPriceAlerts = () => {
    if (!isAuthenticated || !user) return [];
    return priceAlerts.filter(alert => alert.userId === user.id);
  };

  const getActivePriceAlerts = () => {
    if (!isAuthenticated || !user) return [];
    return priceAlerts.filter(alert => alert.userId === user.id && alert.isActive);
  };

  const getPriceHistory = (productId) => {
    return priceHistory[productId] || null;
  };

  const getCurrentProductPrice = (productId) => {
    const history = priceHistory[productId];
    return history ? history.currentPrice : null;
  };

  const addPricePoint = (productId, price) => {
    const currentHistory = priceHistory[productId] || {
      productId: parseInt(productId),
      history: [],
      currentPrice: price,
      originalPrice: price,
      lowestPrice: price,
      highestPrice: price,
      averagePrice: price,
      priceDropPercentage: 0
    };

    const newPricePoint = {
      date: new Date(),
      price: parseInt(price)
    };

    const updatedHistory = [...currentHistory.history, newPricePoint];
    const prices = updatedHistory.map(h => h.price);
    
    const updatedPriceHistory = {
      ...currentHistory,
      history: updatedHistory,
      currentPrice: price,
      lowestPrice: Math.min(...prices),
      highestPrice: Math.max(...prices),
      averagePrice: Math.round(prices.reduce((a, b) => a + b, 0) / prices.length),
      priceDropPercentage: ((currentHistory.originalPrice - price) / currentHistory.originalPrice) * 100,
      lastUpdated: new Date()
    };

    const newPriceHistory = {
      ...priceHistory,
      [productId]: updatedPriceHistory
    };

    setPriceHistory(newPriceHistory);
    localStorage.setItem('priceHistory', JSON.stringify(newPriceHistory));
  };

  const checkPriceAlerts = () => {
    const activeAlerts = getActivePriceAlerts();
    
    activeAlerts.forEach(alert => {
      const currentPrice = getCurrentProductPrice(alert.productId);
      
      if (currentPrice && currentPrice <= alert.targetPrice && !alert.priceReached) {
        // Price target reached!
        triggerPriceAlert(alert, currentPrice);
      }
      
      // Update current price in alert
      updatePriceAlert(alert.id, { 
        currentPrice,
        lastChecked: new Date()
      });
    });
  };

  const triggerPriceAlert = (alert, newPrice) => {
    // Mark alert as price reached
    updatePriceAlert(alert.id, { 
      priceReached: true,
      triggeredAt: new Date(),
      triggeredPrice: newPrice
    });

    // Show notification
    showNotification(
      ` Price Alert: ${alert.productName} is now ${newPrice.toLocaleString('en-IN')}!`,
      'success',
      5000
    );

    // In a real app, this would also send email/push notifications
    console.log('Price alert triggered:', alert);
  };

  const getPriceDropDeals = () => {
    return Object.values(priceHistory)
      .filter(history => history.priceDropPercentage > 10)
      .sort((a, b) => b.priceDropPercentage - a.priceDropPercentage)
      .slice(0, 10);
  };

  const calculatePriceTrend = (productId, days = 7) => {
    const history = priceHistory[productId];
    if (!history || history.history.length < 2) return 'stable';

    const cutoffDate = new Date(Date.now() - days * 24 * 60 * 60 * 1000);
    const recentPrices = history.history
      .filter(h => new Date(h.date) >= cutoffDate)
      .map(h => h.price);

    if (recentPrices.length < 2) return 'stable';

    const firstPrice = recentPrices[0];
    const lastPrice = recentPrices[recentPrices.length - 1];
    const change = ((lastPrice - firstPrice) / firstPrice) * 100;

    if (change > 2) return 'rising';
    if (change < -2) return 'falling';
    return 'stable';
  };

  const getPricePrediction = (productId) => {
    const history = priceHistory[productId];
    if (!history || history.history.length < 3) return null;

    const recentPrices = history.history.slice(-5).map(h => h.price);
    const trend = recentPrices.reduce((acc, price, index) => {
      if (index === 0) return acc;
      return acc + (price - recentPrices[index - 1]);
    }, 0) / (recentPrices.length - 1);

    const predictedPrice = Math.round(history.currentPrice + trend * 7); // 7 days prediction
    
    return {
      predictedPrice,
      confidence: Math.min(90, 50 + (history.history.length * 2)), // Higher confidence with more data
      trend: trend > 0 ? 'rising' : trend < 0 ? 'falling' : 'stable',
      recommendation: predictedPrice < history.currentPrice ? 'wait' : 'buy_now'
    };
  };

  const value = {
    priceAlerts,
    priceHistory,
    isLoading,
    createPriceAlert,
    updatePriceAlert,
    deletePriceAlert,
    getUserPriceAlerts,
    getActivePriceAlerts,
    getPriceHistory,
    getCurrentProductPrice,
    addPricePoint,
    checkPriceAlerts,
    getPriceDropDeals,
    calculatePriceTrend,
    getPricePrediction
  };

  return (
    <PriceTrackingContext.Provider value={value}>
      {children}
    </PriceTrackingContext.Provider>
  );
};

export default PriceTrackingContext;