import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAISearch } from '../../hooks/useAISearch';
import { 
  MagnifyingGlassIcon, 
  XMarkIcon,
  ClockIcon,
  FireIcon,
  SparklesIcon,
  AdjustmentsHorizontalIcon
} from '@heroicons/react/24/outline';

const SmartSearchBar = ({ 
  placeholder = "Search for products...", 
  className = "",
  showSuggestions = true,
  showTrending = true,
  onSearch = null,
  autoFocus = false 
}) => {
  const navigate = useNavigate();
  const { 
    performSearch, 
    getSearchSuggestions, 
    searchState, 
    clearSearch 
  } = useAISearch();
  
  const [inputValue, setInputValue] = useState('');
  const [showDropdown, setShowDropdown] = useState(false);
  const [suggestions, setSuggestions] = useState([]);
  const [highlightedIndex, setHighlightedIndex] = useState(-1);
  
  const inputRef = useRef(null);
  const dropdownRef = useRef(null);

  // Handle input change and generate suggestions
  useEffect(() => {
    if (inputValue.length >= 2 && showSuggestions) {
      const newSuggestions = getSearchSuggestions(inputValue);
      setSuggestions(newSuggestions);
    } else {
      setSuggestions([]);
    }
  }, [inputValue, getSearchSuggestions, showSuggestions]);

  // Handle search submission
  const handleSearch = async (query = inputValue) => {
    if (!query.trim()) return;

    setShowDropdown(false);
    setHighlightedIndex(-1);
    
    if (onSearch) {
      onSearch(query);
    } else {
      await performSearch(query);
      navigate(`/search?q=${encodeURIComponent(query)}`);
    }
  };

  // Handle input change
  const handleInputChange = (e) => {
    const value = e.target.value;
    setInputValue(value);
    setShowDropdown(value.length > 0);
    setHighlightedIndex(-1);
  };

  // Handle key navigation
  const handleKeyDown = (e) => {
    const totalSuggestions = suggestions.length + 
      (showTrending ? searchState.trendingSearches.length : 0);

    switch (e.key) {
      case 'ArrowDown':
        e.preventDefault();
        setHighlightedIndex(prev => 
          prev < totalSuggestions - 1 ? prev + 1 : -1
        );
        break;
      case 'ArrowUp':
        e.preventDefault();
        setHighlightedIndex(prev => 
          prev > -1 ? prev - 1 : totalSuggestions - 1
        );
        break;
      case 'Enter':
        e.preventDefault();
        if (highlightedIndex >= 0) {
          const allSuggestions = [
            ...suggestions.map(s => s.text),
            ...(showTrending ? searchState.trendingSearches : [])
          ];
          handleSearch(allSuggestions[highlightedIndex]);
        } else {
          handleSearch();
        }
        break;
      case 'Escape':
        setShowDropdown(false);
        setHighlightedIndex(-1);
        inputRef.current?.blur();
        break;
    }
  };

  // Handle suggestion click
  const handleSuggestionClick = (suggestion) => {
    setInputValue(suggestion);
    handleSearch(suggestion);
  };

  // Handle clear input
  const handleClear = () => {
    setInputValue('');
    setShowDropdown(false);
    setSuggestions([]);
    setHighlightedIndex(-1);
    clearSearch();
    inputRef.current?.focus();
  };

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setShowDropdown(false);
        setHighlightedIndex(-1);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  // Auto focus if requested
  useEffect(() => {
    if (autoFocus && inputRef.current) {
      inputRef.current.focus();
    }
  }, [autoFocus]);

  return (
    <div className={`relative ${className}`} ref={dropdownRef}>
      {/* Search Input */}
      <div className="relative">
        <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
          <MagnifyingGlassIcon className="h-5 w-5 text-gray-400" />
        </div>
        
        <input
          ref={inputRef}
          type="text"
          value={inputValue}
          onChange={handleInputChange}
          onKeyDown={handleKeyDown}
          onFocus={() => setShowDropdown(inputValue.length > 0)}
          placeholder={placeholder}
          className="w-full pl-10 pr-10 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none transition-all duration-200 text-gray-900 placeholder-gray-500"
        />

        {/* Clear Button */}
        {inputValue && (
          <button
            onClick={handleClear}
            className="absolute inset-y-0 right-0 pr-3 flex items-center text-gray-400 hover:text-gray-600 transition-colors"
          >
            <XMarkIcon className="h-5 w-5" />
          </button>
        )}

        {/* AI Search Indicator */}
        <div className="absolute -bottom-1 right-2 bg-gradient-to-r from-purple-500 to-blue-500 text-white text-xs px-2 py-0.5 rounded-full">
          <div className="flex items-center space-x-1">
            <SparklesIcon className="h-3 w-3" />
            <span>AI</span>
          </div>
        </div>
      </div>

      {/* Search Dropdown */}
      {showDropdown && (
        <div className="absolute z-50 w-full mt-2 bg-white border border-gray-200 rounded-lg shadow-lg max-h-96 overflow-y-auto">
          {/* Search Suggestions */}
          {suggestions.length > 0 && (
            <div className="p-2">
              <div className="flex items-center space-x-2 px-3 py-2 text-xs font-medium text-gray-500 uppercase tracking-wide">
                <SparklesIcon className="h-4 w-4" />
                <span>Smart Suggestions</span>
              </div>
              {suggestions.map((suggestion, index) => (
                <button
                  key={`suggestion-${index}`}
                  onClick={() => handleSuggestionClick(suggestion.text)}
                  className={`w-full text-left px-3 py-2 rounded-md transition-colors ${
                    index === highlightedIndex 
                      ? 'bg-blue-50 text-blue-700' 
                      : 'text-gray-700 hover:bg-gray-50'
                  }`}
                >
                  <div className="flex items-center justify-between">
                    <span className="font-medium">{suggestion.text}</span>
                    <span className="text-xs text-gray-400 bg-gray-100 px-2 py-1 rounded">
                      {suggestion.reason}
                    </span>
                  </div>
                </button>
              ))}
            </div>
          )}

          {/* Trending Searches */}
          {showTrending && searchState.trendingSearches.length > 0 && inputValue.length === 0 && (
            <div className="p-2 border-t border-gray-100">
              <div className="flex items-center space-x-2 px-3 py-2 text-xs font-medium text-gray-500 uppercase tracking-wide">
                <FireIcon className="h-4 w-4" />
                <span>Trending Now</span>
              </div>
              {searchState.trendingSearches.slice(0, 5).map((trend, index) => {
                const adjustedIndex = suggestions.length + index;
                return (
                  <button
                    key={`trending-${index}`}
                    onClick={() => handleSuggestionClick(trend)}
                    className={`w-full text-left px-3 py-2 rounded-md transition-colors ${
                      adjustedIndex === highlightedIndex 
                        ? 'bg-red-50 text-red-700' 
                        : 'text-gray-700 hover:bg-gray-50'
                    }`}
                  >
                    <div className="flex items-center space-x-2">
                      <FireIcon className="h-4 w-4 text-red-500" />
                      <span>{trend}</span>
                    </div>
                  </button>
                );
              })}
            </div>
          )}

          {/* Recent Searches */}
          {searchState.searchHistory.length > 0 && inputValue.length === 0 && (
            <div className="p-2 border-t border-gray-100">
              <div className="flex items-center space-x-2 px-3 py-2 text-xs font-medium text-gray-500 uppercase tracking-wide">
                <ClockIcon className="h-4 w-4" />
                <span>Recent Searches</span>
              </div>
              {searchState.searchHistory.slice(0, 3).map((recent, index) => (
                <button
                  key={`recent-${index}`}
                  onClick={() => handleSuggestionClick(recent)}
                  className="w-full text-left px-3 py-2 rounded-md text-gray-700 hover:bg-gray-50 transition-colors"
                >
                  <div className="flex items-center space-x-2">
                    <ClockIcon className="h-4 w-4 text-gray-400" />
                    <span>{recent}</span>
                  </div>
                </button>
              ))}
            </div>
          )}

          {/* Advanced Search Link */}
          <div className="p-2 border-t border-gray-100">
            <button
              onClick={() => {
                navigate('/advanced-search');
                setShowDropdown(false);
              }}
              className="w-full text-left px-3 py-2 rounded-md text-blue-600 hover:bg-blue-50 transition-colors"
            >
              <div className="flex items-center space-x-2">
                <AdjustmentsHorizontalIcon className="h-4 w-4" />
                <span className="font-medium">Advanced Search</span>
              </div>
            </button>
          </div>

          {/* No suggestions message */}
          {inputValue.length >= 2 && suggestions.length === 0 && (
            <div className="p-4 text-center text-gray-500">
              <SparklesIcon className="h-8 w-8 mx-auto mb-2 text-gray-300" />
              <p>No suggestions found</p>
              <p className="text-sm">Try a different search term</p>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default SmartSearchBar;