import React, { createContext, useContext, useState, useEffect } from 'react';
import { useNotification } from './NotificationContext';

const PWAContext = createContext();

export const usePWA = () => {
  const context = useContext(PWAContext);
  if (!context) {
    throw new Error('usePWA must be used within a PWAProvider');
  }
  return context;
};

export const PWAProvider = ({ children }) => {
  const { showNotification } = useNotification();
  
  // PWA State
  const [isInstallable, setIsInstallable] = useState(false);
  const [isInstalled, setIsInstalled] = useState(false);
  const [isOnline, setIsOnline] = useState(navigator.onLine);
  const [deferredPrompt, setDeferredPrompt] = useState(null);
  const [updateAvailable, setUpdateAvailable] = useState(false);
  const [serviceWorkerRegistration, setServiceWorkerRegistration] = useState(null);
  
  // Device capabilities
  const [capabilities, setCapabilities] = useState({
    share: 'share' in navigator,
    camera: 'mediaDevices' in navigator && 'getUserMedia' in navigator.mediaDevices,
    geolocation: 'geolocation' in navigator,
    notification: 'Notification' in window,
    vibration: 'vibrate' in navigator,
    fullscreen: 'requestFullscreen' in document.documentElement,
    clipboard: 'clipboard' in navigator
  });

  // Installation metrics
  const [installMetrics, setInstallMetrics] = useState({
    promptsShown: 0,
    installsSuccessful: 0,
    installsDeclined: 0,
    lastPromptDate: null
  });

  useEffect(() => {
    // Register Service Worker
    registerServiceWorker();
    
    // Setup PWA event listeners
    setupPWAListeners();
    
    // Check if already installed
    checkInstallationStatus();
    
    // Setup network status listeners
    setupNetworkListeners();
    
    // Load metrics from localStorage
    loadInstallMetrics();
    
    return () => {
      window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
      window.removeEventListener('appinstalled', handleAppInstalled);
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  const registerServiceWorker = async () => {
    if ('serviceWorker' in navigator) {
      try {
        const registration = await navigator.serviceWorker.register('/sw.js', {
          scope: '/'
        });
        
        setServiceWorkerRegistration(registration);
        console.log('PWA: Service Worker registered successfully');

        // Check for updates
        registration.addEventListener('updatefound', () => {
          const newWorker = registration.installing;
          if (newWorker) {
            newWorker.addEventListener('statechange', () => {
              if (newWorker.state === 'installed' && navigator.serviceWorker.controller) {
                setUpdateAvailable(true);
                showNotification('info', 'App update available! Refresh to get the latest features.');
              }
            });
          }
        });

        // Handle service worker messages
        navigator.serviceWorker.addEventListener('message', handleServiceWorkerMessage);
        
      } catch (error) {
        console.error('PWA: Service Worker registration failed:', error);
      }
    }
  };

  const setupPWAListeners = () => {
    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
    window.addEventListener('appinstalled', handleAppInstalled);
  };

  const setupNetworkListeners = () => {
    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);
  };

  const handleBeforeInstallPrompt = (event) => {
    event.preventDefault();
    setDeferredPrompt(event);
    setIsInstallable(true);
    console.log('PWA: Install prompt available');
  };

  const handleAppInstalled = (event) => {
    console.log('PWA: App was installed', event);
    setIsInstalled(true);
    setIsInstallable(false);
    setDeferredPrompt(null);
    
    // Track successful installation
    const newMetrics = {
      ...installMetrics,
      installsSuccessful: installMetrics.installsSuccessful + 1
    };
    setInstallMetrics(newMetrics);
    saveInstallMetrics(newMetrics);
    
    showNotification('success', 'App installed successfully! Welcome to Amazon Clone PWA!');
  };

  const handleOnline = () => {
    setIsOnline(true);
    showNotification('success', 'You\'re back online!');
    
    // Trigger background sync if available
    if (serviceWorkerRegistration && 'sync' in serviceWorkerRegistration) {
      serviceWorkerRegistration.sync.register('background-sync-cart');
      serviceWorkerRegistration.sync.register('background-sync-wishlist');
      serviceWorkerRegistration.sync.register('background-sync-orders');
    }
  };

  const handleOffline = () => {
    setIsOnline(false);
    showNotification('warning', 'You\'re offline. Some features may be limited.');
  };

  const handleServiceWorkerMessage = (event) => {
    if (event.data && event.data.type === 'UPDATE_AVAILABLE') {
      setUpdateAvailable(true);
    }
  };

  const checkInstallationStatus = () => {
    // Check if running in standalone mode (installed)
    if (window.matchMedia('(display-mode: standalone)').matches) {
      setIsInstalled(true);
    }
    
    // Check if running as PWA on iOS
    if (window.navigator.standalone === true) {
      setIsInstalled(true);
    }
  };

  const loadInstallMetrics = () => {
    try {
      const saved = localStorage.getItem('pwa-install-metrics');
      if (saved) {
        setInstallMetrics(JSON.parse(saved));
      }
    } catch (error) {
      console.error('Failed to load install metrics:', error);
    }
  };

  const saveInstallMetrics = (metrics) => {
    try {
      localStorage.setItem('pwa-install-metrics', JSON.stringify(metrics));
    } catch (error) {
      console.error('Failed to save install metrics:', error);
    }
  };

  // PWA Functions
  const promptInstall = async () => {
    if (!deferredPrompt) {
      showNotification('warning', 'Install prompt not available');
      return false;
    }

    try {
      // Show the install prompt
      deferredPrompt.prompt();
      
      // Track prompt shown
      const newMetrics = {
        ...installMetrics,
        promptsShown: installMetrics.promptsShown + 1,
        lastPromptDate: new Date().toISOString()
      };
      setInstallMetrics(newMetrics);
      saveInstallMetrics(newMetrics);

      // Wait for the user's response
      const { outcome } = await deferredPrompt.userChoice;
      
      if (outcome === 'accepted') {
        console.log('PWA: User accepted the install prompt');
        return true;
      } else {
        console.log('PWA: User dismissed the install prompt');
        // Track declined installation
        const declinedMetrics = {
          ...newMetrics,
          installsDeclined: installMetrics.installsDeclined + 1
        };
        setInstallMetrics(declinedMetrics);
        saveInstallMetrics(declinedMetrics);
        return false;
      }
    } catch (error) {
      console.error('PWA: Install prompt failed:', error);
      return false;
    } finally {
      setDeferredPrompt(null);
      setIsInstallable(false);
    }
  };

  const updateApp = () => {
    if (serviceWorkerRegistration && serviceWorkerRegistration.waiting) {
      serviceWorkerRegistration.waiting.postMessage({ type: 'SKIP_WAITING' });
      window.location.reload();
    }
  };

  const requestNotificationPermission = async () => {
    if (!capabilities.notification) {
      return 'not-supported';
    }

    try {
      const permission = await Notification.requestPermission();
      return permission;
    } catch (error) {
      console.error('Failed to request notification permission:', error);
      return 'denied';
    }
  };

  const shareContent = async (shareData) => {
    if (capabilities.share) {
      try {
        await navigator.share(shareData);
        return true;
      } catch (error) {
        console.error('Share failed:', error);
        return false;
      }
    }
    return false;
  };

  const vibrate = (pattern = [200]) => {
    if (capabilities.vibration) {
      navigator.vibrate(pattern);
      return true;
    }
    return false;
  };

  const getCurrentPosition = () => {
    return new Promise((resolve, reject) => {
      if (!capabilities.geolocation) {
        reject(new Error('Geolocation not supported'));
        return;
      }

      navigator.geolocation.getCurrentPosition(
        position => resolve(position),
        error => reject(error),
        { enableHighAccuracy: true, timeout: 10000, maximumAge: 60000 }
      );
    });
  };

  const requestCameraAccess = async () => {
    if (!capabilities.camera) {
      throw new Error('Camera not supported');
    }

    try {
      const stream = await navigator.mediaDevices.getUserMedia({ 
        video: { facingMode: 'environment' } 
      });
      return stream;
    } catch (error) {
      console.error('Camera access failed:', error);
      throw error;
    }
  };

  const enterFullscreen = async () => {
    if (capabilities.fullscreen) {
      try {
        await document.documentElement.requestFullscreen();
        return true;
      } catch (error) {
        console.error('Fullscreen failed:', error);
        return false;
      }
    }
    return false;
  };

  const exitFullscreen = async () => {
    if (document.fullscreenElement) {
      try {
        await document.exitFullscreen();
        return true;
      } catch (error) {
        console.error('Exit fullscreen failed:', error);
        return false;
      }
    }
    return false;
  };

  const copyToClipboard = async (text) => {
    if (capabilities.clipboard) {
      try {
        await navigator.clipboard.writeText(text);
        return true;
      } catch (error) {
        console.error('Clipboard write failed:', error);
        return false;
      }
    }
    return false;
  };

  // Background sync for offline actions
  const addOfflineAction = (type, action) => {
    try {
      const key = `offline-actions-${type}`;
      const existing = JSON.parse(localStorage.getItem(key) || '[]');
      existing.push({
        ...action,
        timestamp: Date.now(),
        id: Math.random().toString(36).substr(2, 9)
      });
      localStorage.setItem(key, JSON.stringify(existing));
      
      // Register background sync if available
      if (serviceWorkerRegistration && 'sync' in serviceWorkerRegistration) {
        serviceWorkerRegistration.sync.register(`background-sync-${type}`);
      }
    } catch (error) {
      console.error(`Failed to add offline action for ${type}:`, error);
    }
  };

  const value = {
    // State
    isInstallable,
    isInstalled,
    isOnline,
    updateAvailable,
    capabilities,
    installMetrics,
    
    // Functions
    promptInstall,
    updateApp,
    requestNotificationPermission,
    shareContent,
    vibrate,
    getCurrentPosition,
    requestCameraAccess,
    enterFullscreen,
    exitFullscreen,
    copyToClipboard,
    addOfflineAction
  };

  return (
    <PWAContext.Provider value={value}>
      {children}
    </PWAContext.Provider>
  );
};