import React, { useEffect, useCallback, useContext } from 'react';
import { useLocation } from 'react-router-dom';
import advancedAnalytics from '../services/advancedAnalytics';
import { useCart } from '../context/CartContext';
import { useAuth } from '../context/AuthContext';

export const useAdvancedAnalytics = () => {
  const location = useLocation();
  const { cart } = useCart();
  const { user } = useAuth();

  // Track page views automatically
  useEffect(() => {
    advancedAnalytics.trackEvent('page_view', {
      path: location.pathname,
      search: location.search,
      referrer: document.referrer,
      userId: user?.id
    });
  }, [location, user]);

  // Analytics methods
  const trackProductView = useCallback((product) => {
    advancedAnalytics.trackProductView({
      ...product,
      userId: user?.id
    });
  }, [user]);

  const trackAddToCart = useCallback((product, quantity = 1) => {
    advancedAnalytics.trackAddToCart({
      ...product,
      userId: user?.id
    }, quantity);
  }, [user]);

  const trackPurchase = useCallback((orderData) => {
    advancedAnalytics.trackPurchase({
      ...orderData,
      customerId: user?.id,
      userId: user?.id
    });
  }, [user]);

  const trackSearch = useCallback((query, results = []) => {
    advancedAnalytics.trackSearchQuery(query, results);
  }, []);

  const trackCustomEvent = useCallback((eventType, eventData = {}) => {
    advancedAnalytics.trackEvent(eventType, {
      ...eventData,
      userId: user?.id
    });
  }, [user]);

  const trackUserInteraction = useCallback((action, element, data = {}) => {
    advancedAnalytics.trackEvent('user_interaction', {
      action,
      element,
      ...data,
      userId: user?.id
    });
  }, [user]);

  const trackFormSubmission = useCallback((formName, success = true, errors = []) => {
    advancedAnalytics.trackEvent('form_submission', {
      formName,
      success,
      errors,
      userId: user?.id
    });
  }, [user]);

  const trackError = useCallback((error, context = {}) => {
    advancedAnalytics.trackEvent('error', {
      message: error.message,
      stack: error.stack,
      context,
      userId: user?.id
    });
  }, [user]);

  const trackPerformance = useCallback((metric, value, context = {}) => {
    advancedAnalytics.trackEvent('performance', {
      metric,
      value,
      context,
      userId: user?.id
    });
  }, [user]);

  const trackABTest = useCallback((testName, variant, conversion = false) => {
    advancedAnalytics.trackEvent('ab_test', {
      testName,
      variant,
      conversion,
      userId: user?.id
    });
  }, [user]);

  // Get analytics data
  const getRealtimeMetrics = useCallback(() => {
    return advancedAnalytics.getRealTimeMetrics();
  }, []);

  const getRevenueMetrics = useCallback((timeRange = 30) => {
    return advancedAnalytics.calculateRevenueMetrics(timeRange);
  }, []);

  const getProductPerformance = useCallback(() => {
    return advancedAnalytics.analyzeProductPerformance();
  }, []);

  const getCustomerSegments = useCallback(() => {
    return advancedAnalytics.segmentCustomers();
  }, []);

  const getUserJourney = useCallback((sessionId) => {
    return advancedAnalytics.analyzeUserJourney(sessionId);
  }, []);

  const predictCustomerLTV = useCallback((customerId) => {
    return advancedAnalytics.predictCustomerLifetimeValue(customerId);
  }, []);

  const exportData = useCallback((format = 'json') => {
    return advancedAnalytics.exportAnalyticsData(format);
  }, []);

  // A/B Testing
  const createABTest = useCallback((testName, variants) => {
    return advancedAnalytics.createABTest(testName, variants);
  }, []);

  const assignUserToVariant = useCallback((testId, userId) => {
    return advancedAnalytics.assignUserToVariant(testId, userId);
  }, []);

  return {
    // Event tracking
    trackProductView,
    trackAddToCart,
    trackPurchase,
    trackSearch,
    trackCustomEvent,
    trackUserInteraction,
    trackFormSubmission,
    trackError,
    trackPerformance,
    trackABTest,
    
    // Data retrieval
    getRealtimeMetrics,
    getRevenueMetrics,
    getProductPerformance,
    getCustomerSegments,
    getUserJourney,
    predictCustomerLTV,
    exportData,
    
    // A/B Testing
    createABTest,
    assignUserToVariant,
    
    // Direct access to analytics instance
    analytics: advancedAnalytics
  };
};

// Higher-order component for automatic analytics tracking
export const withAnalytics = (WrappedComponent) => {
  return function AnalyticsWrapper(props) {
    const analytics = useAdvancedAnalytics();
    
    useEffect(() => {
      // Track component mount
      analytics.trackCustomEvent('component_mount', {
        componentName: WrappedComponent.name || 'Unknown'
      });
      
      return () => {
        // Track component unmount
        analytics.trackCustomEvent('component_unmount', {
          componentName: WrappedComponent.name || 'Unknown'
        });
      };
    }, [analytics]);
    
    return React.createElement(WrappedComponent, { ...props, analytics });
  };
};

// Hook for A/B testing
export const useABTest = (testName, variants, defaultVariant = null) => {
  const { user } = useAuth();
  const analytics = useAdvancedAnalytics();
  
  const userId = user?.id || 'anonymous_' + Date.now();
  
  useEffect(() => {
    if (variants && variants.length > 0) {
      // Create test if it doesn't exist
      const existingTests = JSON.parse(localStorage.getItem('ab_tests') || '{}');
      const existingTest = Object.values(existingTests).find(test => test.name === testName);
      
      if (!existingTest) {
        analytics.createABTest(testName, variants);
      }
    }
  }, [testName, variants, analytics]);
  
  const assignedVariant = useCallback(() => {
    const tests = JSON.parse(localStorage.getItem('ab_tests') || '{}');
    const test = Object.values(tests).find(t => t.name === testName);
    
    if (!test) return defaultVariant;
    
    return analytics.assignUserToVariant(test.id, userId) || defaultVariant;
  }, [testName, userId, defaultVariant, analytics]);
  
  const trackConversion = useCallback((conversionData = {}) => {
    const variant = assignedVariant();
    analytics.trackABTest(testName, variant, true);
    analytics.trackCustomEvent('ab_test_conversion', {
      testName,
      variant,
      ...conversionData
    });
  }, [testName, assignedVariant, analytics]);
  
  return {
    variant: assignedVariant(),
    trackConversion
  };
};

// Hook for performance monitoring
export const usePerformanceMonitoring = () => {
  const analytics = useAdvancedAnalytics();
  
  useEffect(() => {
    // Track page load performance
    if (typeof window !== 'undefined' && window.performance) {
      const navigation = performance.getEntriesByType('navigation')[0];
      if (navigation) {
        analytics.trackPerformance('page_load_time', navigation.loadEventEnd - navigation.loadEventStart);
        analytics.trackPerformance('dom_content_loaded', navigation.domContentLoadedEventEnd - navigation.domContentLoadedEventStart);
        analytics.trackPerformance('first_paint', navigation.loadEventEnd - navigation.fetchStart);
      }
    }
    
    // Track memory usage if available
    if (performance.memory) {
      analytics.trackPerformance('memory_used', performance.memory.usedJSHeapSize);
      analytics.trackPerformance('memory_total', performance.memory.totalJSHeapSize);
    }
  }, [analytics]);
  
  const trackCustomPerformance = useCallback((metric, value, context = {}) => {
    analytics.trackPerformance(metric, value, context);
  }, [analytics]);
  
  return {
    trackCustomPerformance
  };
};

export default useAdvancedAnalytics;