import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { AuthProvider } from './context/AuthContext';
import { CartProvider } from './context/CartContext';
import { ProductProvider } from './context/ProductContext';
import { WishlistProvider } from './context/WishlistContext';
import { NotificationProvider } from './context/NotificationContext';
import { SearchProvider } from './context/SearchContext';
import { ReviewsProvider } from './context/ReviewsContext';
import { OrdersProvider } from './context/OrdersContext';
import { PaymentProvider } from './context/PaymentContext';
import { UserProfileProvider } from './context/UserProfileContext';
import { PriceTrackingProvider } from './context/PriceTrackingContext';
import { AnalyticsProvider } from './context/AnalyticsContext';
import { ChatProvider } from './context/ChatContext';
import { AdvancedSearchProvider } from './context/AdvancedSearchContext';
import { RecommendationProvider } from './context/RecommendationContext';
import { PWAProvider } from './context/PWAContext';

// Components
import Layout from './components/Layout/Layout';

// Pages
import HomePage from './pages/HomePage';
import ProductsPage from './pages/ProductsPage';
import CartPage from './pages/CartPage';
import GamingPage from './pages/GamingPage';
import FashionPage from './pages/FashionPage';
import DealsPage from './pages/DealsPage';
import WishlistPage from './pages/WishlistPage';
import HomeLivingPage from './pages/HomeLivingPage';
import HealthSportsPage from './pages/HealthSportsPage';
import KidsBabyPage from './pages/KidsBabyPage';
import AutomotiveToolsPage from './pages/AutomotiveToolsPage';
import SearchPage from './pages/SearchPage';
import AISearchResultsPage from './components/AI/AISearchResultsPage';
import CheckoutPage from './pages/CheckoutPage';
import OrderConfirmationPage from './pages/OrderConfirmationPage';
import OrdersPageEnhanced from './pages/OrdersPage';
import AuthPage from './pages/AuthPage';
import AddressesPage from './pages/AddressesPage';
import ProductDetailPage from './pages/ProductDetailPage';
import UserProfilePage from './pages/UserProfilePage';
import AnalyticsDashboard from './pages/AnalyticsDashboard';
import AdvancedAnalyticsPage from './pages/AdvancedAnalyticsPage';
import AdvancedSearchPage from './pages/AdvancedSearchPage';
import RecommendationDashboard from './pages/RecommendationDashboard';
import BestSellersPage from './pages/BestSellersPage';
import AllProductsPage from './pages/AllProductsPage';

// Components
import LiveChatSupport from './components/LiveChatSupport';
import BottomNavigation from './components/Mobile/BottomNavigation';
import InstallPrompt from './components/PWA/InstallPrompt';
import OfflineIndicator from './components/PWA/OfflineIndicator';
import AIChatbot from './components/AI/AIChatbot';

// Placeholder components for remaining pages
const CustomerServicePage = () => <div className="p-8"><h1 className="text-3xl font-bold">Customer Service</h1><p>Customer service content coming soon...</p></div>;
const SellPage = () => <div className="p-8"><h1 className="text-3xl font-bold">Sell on Amazon</h1><p>Selling information coming soon...</p></div>;

function App() {
  return (
    <NotificationProvider>
      <AuthProvider>
        <ProductProvider>
          <SearchProvider>
            <CartProvider>
              <WishlistProvider>
                <ReviewsProvider>
                  <OrdersProvider>
                    <PaymentProvider>
                      <UserProfileProvider>
                        <PriceTrackingProvider>
                          <AnalyticsProvider>
                            <ChatProvider>
                              <AdvancedSearchProvider>
                                <RecommendationProvider>
                                  <PWAProvider>
                                    <Router>
                                      <OfflineIndicator />
                                      <Routes>
                                        <Route path="/" element={<Layout />}>
                                          <Route index element={<HomePage />} />
                                          <Route path="gaming" element={<GamingPage />} />
                                          <Route path="home-living" element={<HomeLivingPage />} />
                                          <Route path="fashion" element={<FashionPage />} />
                                          <Route path="health-sports" element={<HealthSportsPage />} />
                                          <Route path="kids-baby" element={<KidsBabyPage />} />
                                          <Route path="automotive-tools" element={<AutomotiveToolsPage />} />
                                          <Route path="products" element={<ProductsPage />} />
                                          <Route path="all-products" element={<AllProductsPage />} />
                                          <Route path="product/:id" element={<ProductDetailPage />} />
                                          <Route path="deals" element={<DealsPage />} />
                                          <Route path="bestsellers" element={<BestSellersPage />} />
                                          <Route path="cart" element={<CartPage />} />
                                          <Route path="wishlist" element={<WishlistPage />} />
                                          <Route path="search" element={<AISearchResultsPage />} />
                                          <Route path="login" element={<AuthPage />} />
                                          <Route path="register" element={<AuthPage />} />
                                          <Route path="checkout" element={<CheckoutPage />} />
                                          <Route path="order-confirmation" element={<OrderConfirmationPage />} />
                                          <Route path="profile" element={<UserProfilePage />} />
                                          <Route path="analytics" element={<AnalyticsDashboard />} />
                                          <Route path="advanced-analytics" element={<AdvancedAnalyticsPage />} />
                                          <Route path="advanced-search" element={<AdvancedSearchPage />} />
                                          <Route path="recommendations" element={<RecommendationDashboard />} />
                                          <Route path="orders" element={<OrdersPageEnhanced />} />
                                          <Route path="addresses" element={<AddressesPage />} />
                                          <Route path="customer-service" element={<CustomerServicePage />} />
                                          <Route path="sell" element={<SellPage />} />
                                          <Route path="*" element={<HomePage />} />
                                        </Route>
                                      </Routes>
                                      <LiveChatSupport />
                                      <BottomNavigation />
                                      <InstallPrompt />
                                      <AIChatbot />
                                    </Router>
                                  </PWAProvider>
                                </RecommendationProvider>
                              </AdvancedSearchProvider>
                            </ChatProvider>
                          </AnalyticsProvider>
                        </PriceTrackingProvider>
                      </UserProfileProvider>
                    </PaymentProvider>
                  </OrdersProvider>
                </ReviewsProvider>
              </WishlistProvider>
            </CartProvider>
          </SearchProvider>
        </ProductProvider>
      </AuthProvider>
    </NotificationProvider>
  );
}

export default App;