import React, { useState, useEffect } from 'react';
import { usePWA } from '../../context/PWAContext';
import {
  XMarkIcon,
  ArrowDownTrayIcon,
  DevicePhoneMobileIcon,
  SparklesIcon
} from '@heroicons/react/24/outline';

const InstallPrompt = () => {
  const { 
    isInstallable, 
    isInstalled, 
    promptInstall, 
    installMetrics,
    capabilities 
  } = usePWA();
  
  const [showPrompt, setShowPrompt] = useState(false);
  const [dismissed, setDismissed] = useState(false);

  useEffect(() => {
    // Show install prompt based on conditions
    const shouldShow = 
      isInstallable && 
      !isInstalled && 
      !dismissed &&
      installMetrics.promptsShown < 3 && // Limit to 3 prompts
      (!installMetrics.lastPromptDate || 
       Date.now() - new Date(installMetrics.lastPromptDate).getTime() > 7 * 24 * 60 * 60 * 1000); // 7 days

    if (shouldShow) {
      // Delay showing prompt to not interrupt user experience
      const timer = setTimeout(() => {
        setShowPrompt(true);
      }, 10000); // Show after 10 seconds

      return () => clearTimeout(timer);
    }
  }, [isInstallable, isInstalled, dismissed, installMetrics]);

  const handleInstall = async () => {
    const success = await promptInstall();
    if (success) {
      setShowPrompt(false);
    }
  };

  const handleDismiss = () => {
    setDismissed(true);
    setShowPrompt(false);
  };

  const features = [
    {
      icon: DevicePhoneMobileIcon,
      title: 'Native App Experience',
      description: 'Full-screen browsing without browser UI'
    },
    {
      icon: SparklesIcon,
      title: 'Faster Loading',
      description: 'Instant access and improved performance'
    },
    {
      icon: ArrowDownTrayIcon,
      title: 'Offline Access',
      description: 'Browse products even without internet'
    }
  ];

  if (!showPrompt || isInstalled) {
    return null;
  }

  return (
    <>
      {/* Mobile Install Banner */}
      <div className="fixed bottom-20 left-4 right-4 z-50 md:hidden">
        <div className="bg-gradient-to-r from-amazon-blue to-amazon-blue-dark rounded-lg shadow-lg border border-gray-200 p-4 backdrop-blur-sm">
          <div className="flex items-start justify-between">
            <div className="flex items-center space-x-3 flex-1">
              <div className="bg-amazon-orange rounded-lg p-2">
                <ArrowDownTrayIcon className="h-6 w-6 text-white" />
              </div>
              <div className="flex-1 min-w-0">
                <h3 className="text-white font-semibold text-sm">
                  Install Amazon Clone
                </h3>
                <p className="text-blue-100 text-xs mt-1">
                  Get the full app experience with offline browsing
                </p>
              </div>
            </div>
            <button
              onClick={handleDismiss}
              className="text-blue-200 hover:text-white p-1"
            >
              <XMarkIcon className="h-5 w-5" />
            </button>
          </div>
          <div className="flex space-x-2 mt-3">
            <button
              onClick={handleInstall}
              className="flex-1 bg-amazon-orange text-white px-4 py-2 rounded-lg font-medium text-sm hover:bg-orange-600 transition-colors"
            >
              Install App
            </button>
            <button
              onClick={handleDismiss}
              className="px-4 py-2 text-blue-100 text-sm hover:text-white transition-colors"
            >
              Not Now
            </button>
          </div>
        </div>
      </div>

      {/* Desktop Install Modal */}
      <div className="hidden md:block fixed inset-0 z-50 overflow-y-auto">
        <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
          <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
          
          <div className="inline-block align-bottom bg-white rounded-lg px-4 pt-5 pb-4 text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full sm:p-6">
            <div className="absolute top-0 right-0 pt-4 pr-4">
              <button
                onClick={handleDismiss}
                className="bg-white rounded-md text-gray-400 hover:text-gray-600 focus:outline-none"
              >
                <XMarkIcon className="h-6 w-6" />
              </button>
            </div>
            
            <div className="sm:flex sm:items-start">
              <div className="mx-auto flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-amazon-orange sm:mx-0 sm:h-10 sm:w-10">
                <ArrowDownTrayIcon className="h-6 w-6 text-white" />
              </div>
              <div className="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                <h3 className="text-lg leading-6 font-medium text-gray-900">
                  Install Amazon Clone App
                </h3>
                <div className="mt-2">
                  <p className="text-sm text-gray-500">
                    Get the best shopping experience with our Progressive Web App. 
                    Enjoy faster loading, offline browsing, and native app features.
                  </p>
                </div>
              </div>
            </div>
            
            <div className="mt-6 space-y-4">
              {features.map((feature, index) => (
                <div key={index} className="flex items-center space-x-3">
                  <feature.icon className="h-5 w-5 text-amazon-orange flex-shrink-0" />
                  <div>
                    <h4 className="text-sm font-medium text-gray-900">
                      {feature.title}
                    </h4>
                    <p className="text-xs text-gray-500">
                      {feature.description}
                    </p>
                  </div>
                </div>
              ))}
            </div>
            
            <div className="mt-6 flex space-x-3">
              <button
                onClick={handleInstall}
                className="flex-1 bg-amazon-orange text-white px-4 py-2 rounded-lg font-medium hover:bg-orange-600 transition-colors"
              >
                Install App
              </button>
              <button
                onClick={handleDismiss}
                className="flex-1 bg-gray-100 text-gray-700 px-4 py-2 rounded-lg font-medium hover:bg-gray-200 transition-colors"
              >
                Maybe Later
              </button>
            </div>
            
            <p className="mt-3 text-xs text-gray-400 text-center">
              You can always install later from your browser menu
            </p>
          </div>
        </div>
      </div>
    </>
  );
};

export default InstallPrompt;