// Product Provider Component
export const ProductProvider = ({ children }) => {
  const [products, setProducts] = useState(sampleProducts);
  const [loading, setLoading] = useState(false);
  const [filters, setFilters] = useState({
    category: 'all',
    priceRange: { min: 0, max: 300000 },
    rating: 0,
    brand: 'all',
    inStock: false,
    freeShipping: false
  });
  const [searchQuery, setSearchQuery] = useState('');
  const [sortBy, setSortBy] = useState('relevance');
  
  // Filter products based on current filters and search
  const getFilteredProducts = () => {
    let filtered = [...products];
    
    // Search filter
    if (searchQuery) {
      filtered = filtered.filter(product =>
        product.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        product.description.toLowerCase().includes(searchQuery.toLowerCase()) ||
        product.brand.toLowerCase().includes(searchQuery.toLowerCase()) ||
        product.category.toLowerCase().includes(searchQuery.toLowerCase())
      );
    }
    
    // Category filter
    if (filters.category !== 'all') {
      filtered = filtered.filter(product => product.category === filters.category);
    }
    
    // Price range filter
    filtered = filtered.filter(product =>
      product.price >= filters.priceRange.min && product.price <= filters.priceRange.max
    );
    
    // Rating filter
    if (filters.rating > 0) {
      filtered = filtered.filter(product => product.rating >= filters.rating);
    }
    
    // Brand filter
    if (filters.brand !== 'all') {
      filtered = filtered.filter(product => product.brand === filters.brand);
    }
    
    // Stock filter
    if (filters.inStock) {
      filtered = filtered.filter(product => product.inStock);
    }
    
    // Free shipping filter
    if (filters.freeShipping) {
      filtered = filtered.filter(product => product.freeShipping);
    }
    
    // Sort products
    switch (sortBy) {
      case 'price-low':
        filtered.sort((a, b) => a.price - b.price);
        break;
      case 'price-high':
        filtered.sort((a, b) => b.price - a.price);
        break;
      case 'rating':
        filtered.sort((a, b) => b.rating - a.rating);
        break;
      case 'reviews':
        filtered.sort((a, b) => b.reviews - a.reviews);
        break;
      case 'newest':
        filtered.sort((a, b) => b.id - a.id);
        break;
      default:
        // relevance - keep original order
        break;
    }
    
    return filtered;
  };
  
  // Get products by category
  const getProductsByCategory = (category) => {
    return products.filter(product => product.category === category);
  };
  
  // Get product by ID
  const getProductById = (id) => {
    return products.find(product => product.id === parseInt(id));
  };
  
  // Get best sellers (top rated products)
  const getBestSellers = (limit = 20) => {
    return [...products]
      .sort((a, b) => b.rating - a.rating)
      .slice(0, limit);
  };
  
  // Get deals (products with high discounts)
  const getDeals = (limit = 20) => {
    return [...products]
      .filter(product => product.discount > 10)
      .sort((a, b) => b.discount - a.discount)
      .slice(0, limit);
  };
  
  // Get featured products
  const getFeaturedProducts = (limit = 8) => {
    return [...products]
      .filter(product => product.rating >= 4.5)
      .slice(0, limit);
  };
  
  // Get related products (based on category and tags)
  const getRelatedProducts = (productId, limit = 6) => {
    const product = getProductById(productId);
    if (!product) return [];
    
    return products
      .filter(p => p.id !== productId && (
        p.category === product.category || 
        p.brand === product.brand ||
        (p.tags && product.tags && p.tags.some(tag => product.tags.includes(tag)))
      ))
      .sort((a, b) => b.rating - a.rating)
      .slice(0, limit);
  };
  
  // Get frequently bought together
  const getFrequentlyBoughtTogether = (productId, limit = 3) => {
    const product = getProductById(productId);
    if (!product) return [];
    
    // Simulate frequently bought together based on category and price range
    return products
      .filter(p => 
        p.id !== productId && 
        p.category === product.category &&
        Math.abs(p.price - product.price) < product.price * 0.5
      )
      .sort((a, b) => b.rating - a.rating)
      .slice(0, limit);
  };
  
  // Get product variant by ID
  const getProductVariant = (productId, variantId) => {
    const product = getProductById(productId);
    if (!product || !product.variants) return null;
    
    return product.variants.find(variant => variant.id === variantId);
  };
  
  // Check stock availability
  const checkStockAvailability = (productId, quantity = 1, variantId = null) => {
    const product = getProductById(productId);
    if (!product) return { available: false, message: 'Product not found' };
    
    let stock = product.inventory?.available || 0;
    
    if (variantId && product.variants) {
      const variant = product.variants.find(v => v.id === variantId);
      stock = variant?.stock || 0;
    }
    
    if (stock === 0) {
      return { available: false, message: 'Out of stock' };
    }
    
    if (stock < quantity) {
      return { 
        available: false, 
        message: `Only ${stock} items available` 
      };
    }
    
    if (stock <= (product.inventory?.lowStockThreshold || 10)) {
      return { 
        available: true, 
        message: `Only ${stock} left in stock`,
        isLowStock: true 
      };
    }
    
    return { available: true, message: 'In stock' };
  };
  
  // Get price for variant
  const getVariantPrice = (productId, variantId) => {
    const product = getProductById(productId);
    if (!product) return null;
    
    if (variantId && product.variants) {
      const variant = product.variants.find(v => v.id === variantId);
      return variant?.price || product.price;
    }
    
    return product.price;
  };
  
  // Update filters
  const updateFilters = (newFilters) => {
    setFilters(prev => ({ ...prev, ...newFilters }));
  };
  
  // Reset filters
  const resetFilters = () => {
    setFilters({
      category: 'all',
      priceRange: { min: 0, max: 300000 },
      rating: 0,
      brand: 'all',
      inStock: false,
      freeShipping: false
    });
  };
  
  const value = {
    products,
    loading,
    filters,
    searchQuery,
    sortBy,
    getFilteredProducts,
    getProductsByCategory,
    getProductById,
    getBestSellers,
    getDeals,
    getFeaturedProducts,
    getRelatedProducts,
    getFrequentlyBoughtTogether,
    getProductVariant,
    checkStockAvailability,
    getVariantPrice,
    updateFilters,
    resetFilters,
    setSearchQuery,
    setSortBy
  };
  
  return (
    <ProductContext.Provider value={value}>
      {children}
    </ProductContext.Provider>
  );
};

// Custom hook to use product context
export const useProducts = () => {
  const context = useContext(ProductContext);
  if (!context) {
    throw new Error('useProducts must be used within a ProductProvider');
  }
  return context;
};

export default ProductContext;